import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormArrayName, FormControlName, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-filter-log-dropdown',
  templateUrl: './filter-log-dropdown.component.html',
  styleUrls: ['./filter-log-dropdown.component.css']
})
export class FilterLogDropdownComponent {
  @Input()
  fg: FormGroup;

  @Input()
  numberOfItems: number;

  @Input()
  items: [];

  @Input()
  itemNames: [];

  @Input()
  controlName: FormControlName;

  @Input()
  arrayName: FormArrayName;

  @Input()
  dropdownSettings: {};

  @Output()
  onSelected = new EventEmitter<any>();
  @Output()
  onDeselected = new EventEmitter<any>();

  onChange(result:string, isSelected:boolean) {
    console.log("onSelect :",isSelected,"; result :", result);
    if (isSelected) {
      this.onSelected.emit(result);
    } else {
      this.onDeselected.emit(result);
    }
  }
}
