import { Component } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MatDialogRef, MatDialog } from '@angular/material/dialog';
import { Router, ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ConvertUtils } from 'src/app/convert-utils';
import { RouterExtService } from 'src/app/service/route.service';
import { SharedService } from 'src/app/service/shared.service';
import { ConfirmDeleteSystemDialogComponent } from '../../confirm-delete-system-dialog/confirm-delete-system-dialog.component';
import { SystemUtils } from '../../system-utils';
import { Location } from '@angular/common';
import { GroupDetailEditingResult, GroupFilter } from '../group-filter';
import { AddGroupFilterSubmitDialogComponent } from '../add-group-filter-submit-dialog/add-group-filter-submit-dialog.component';
import { Message, MessageUtils } from 'src/app/message';
import { WebsocketService } from 'src/app/service/websocket.service';
import { UserService } from 'src/app/service/user.service';
import { environment } from 'src/environments/environment';
import { AddGroupFailDialogComponent } from '../add-group-fail-dialog/add-group-fail-dialog.component';
import { SystemDetail } from 'src/app/system-detail';
import { GroupDetail } from '../group-detail';
import { GroupFilterUtils } from '../group-filter-utils';

@Component({
  selector: 'app-change-project',
  templateUrl: './add-group-filter.component.html',
  styleUrls: ['./add-group-filter.component.css'],
})
export class AddGroupFilterComponent {
  static route = 'add-group-filter';
  edit = false;
  system?: SystemDetail;
  previousUrl = undefined;
  showMenu = true;
  siteLanguage = 'English';
  languageList = [
    { code: 'en', label: 'English' },
    { code: 'zh_HK', label: '繁體中文' },
    { code: 'zh_CN', label: '简体中文' },
  ];
  currentLanguage?: String;

  lampSelection = new FormControl('');
  lampSelectionList: string[] = [
    'Lamp ON',
    'Lamp Off',
    'Normal',
    'Fault',
    'Unknown',
    'Power Failure',
  ];

  groupSelection = new FormControl('');
  groupSelectionList: string[] = [
    'Critical System',
    'Operation',
    'Spare Kit',
    'Special',
  ];

  systemList: SystemDetail[];
  systemSettingList: SystemDetail[];

  private dialogRef?: MatDialogRef<AddGroupFilterSubmitDialogComponent>;
  private confirmDialogRef?: MatDialogRef<ConfirmDeleteSystemDialogComponent>;
  private eDialogRef?: MatDialogRef<AddGroupFailDialogComponent>;

  myForm: FormGroup;
  projectID?: string;
  updateTime?: string;

  groupFilterDetail: GroupDetail;
  groupFilterDetailArray: GroupDetail[];
  submittedDetail: GroupDetail;

  //environment configuration (refer to /environment)
  isDev: boolean = false;

  convertToSimplifiedChinese() {
    const _nameTC = this.myForm.get('nameTC')?.value;
    this.myForm
      .get('nameSC')
      ?.setValue(ConvertUtils.convertToSimplifiedChinese(_nameTC));
  }

  //get unixtime now
  getUpdateTime() {
    //Get Time API
    const request: RequestInfo = new Request(
      'https://worldtimeapi.org/api/timezone/Asia/Hong_Kong',
      {
        method: 'GET',
      }
    );

    return (
      fetch(request)
        // the JSON body is taken from the response
        .then((res) => {
          let promise = res.json();
          promise.then((value) => {
            console.log('update time : ', value.unixtime);
            this.updateTime = value.unixtime;
          });
        })
        .catch((err) => {
          console.log('get time erorr: ', err);
          this.updateTime = (Date.now() / 1000).toFixed(0);
        })
    );
  }

  getSystemListFromWebsocket() {
    // {
    //   "type": "system_List",
    //   "content": {
    //       "username": "String Type",
    //       "project_ID": "String Type"
    //       }
    // }

    let message: Message = {
      type: '',
      content: {},
    };

    message.type = 'system_List';
    message.content = {
      username: 'String Type',
      project_ID: 'String Type',
    };

    this.websocketService.messages.next(message);
  }

  constructor(
    private translate: TranslateService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private location: Location,
    private dialog: MatDialog,
    private routerExtService: RouterExtService,
    private sharedService: SharedService,
    private websocketService: WebsocketService,
    private userService: UserService
  ) {
    let savedLang = localStorage.getItem('LANG');
    if (savedLang != null) {
      this.siteLanguage = savedLang;
      translate.use(savedLang);
      this.currentLanguage = translate.currentLang;
      console.log('currentLanguage', this.currentLanguage);
    }

    console.log('production? : ' + environment.production);
    this.isDev = !environment.production;

    this.systemList = SystemUtils.getListFromLocalStorage();

    websocketService.reconnect();
    console.log('++++++++ reconnect websocket +++++++++');
    websocketService.messages.subscribe((msg) => {
      console.log('[add-group-filter] Response from websocket: ' + msg);

      if (MessageUtils.isJsonString(msg.toString())) {
        var jObject = JSON.parse(msg.toString());

        if (jObject.type == 'group_DetailEditing_Result') {
          //After submit
          let content = jObject.content;
          let groupEdit = new GroupDetailEditingResult().fromJSON(content);

          //check result == success
          if (groupEdit.result != 'Success') {
            //if fail => show error dialog
            this.showFailedDialog(groupEdit.errorCode);
          } else {
            //if success => save to local storage
            //TODO: Save local storage
            if (groupEdit.action == 'create') {
              GroupFilterUtils.addGroupDetailToLocalStorage(
                this.submittedDetail,
                this.myForm.get('systemId')?.value
              );
            } else if (groupEdit.action == 'edit') {
              GroupFilterUtils.editGroupDetailToLocalStorage(
                this.submittedDetail,
                this.myForm.get('systemId')?.value
              );
            } else {
              //remove
              GroupFilterUtils.removeGroupDetailToLocalStorage(
                this.submittedDetail,
                this.myForm.get('systemId')?.value
              );
            }

            if (this.groupFilterDetail == null) {
              this.openSubmittedDialog();
            } else {
              this.location.back();
            }
          }
        }
      }
    });

    //set isEditSystem to true
    this.sharedService.isEditSystem = true;

    //get previous navigation
    this.routerExtService.getPreviousUrl().then((value) => {
      this.previousUrl = value;
      console.log('previousUrl', this.previousUrl);
    });

    //form validation
    this.myForm = new FormGroup({
      systemId: new FormControl(''),
      nameEN: new FormControl(''),
      nameTC: new FormControl(''),
      nameSC: new FormControl(''),
      sampleResponse: new FormControl(),
    });

    //system empty. Disable system toggle
    if (this.systemList.length == 0) {
      this.myForm.get('systemId').disable();
    } else {
      this.myForm.get('systemId').enable();
    }

    //get session storage here
    const formJsonString = sessionStorage.getItem('groupFormDetail');
    console.log('groupFormDetail : ', formJsonString);
    if (formJsonString != null) {
      const formDetail = JSON.parse(formJsonString);
      this.myForm.get('systemId')?.setValue(formDetail.systemId);
      this.myForm.get('nameEN')?.setValue(formDetail.nameEN);
      this.myForm.get('nameTC')?.setValue(formDetail.nameTC);
      this.myForm.get('nameSC')?.setValue(formDetail.nameSC);
    }

    //To use "edit", use {queryParams : {'edit':true}} in navigator
    this.edit = this.activatedRoute.snapshot.queryParams['edit'] ?? false;
    console.log('edit :', this.edit);
    if (this.edit) {
      this.groupFilterDetail = JSON.parse(
        this.activatedRoute.snapshot.queryParams['groupFilter']
      );
      //prefill the data
      this.myForm.get('systemId')?.setValue(this.groupFilterDetail?.system_ID);
      this.myForm.get('nameEN')?.setValue(this.groupFilterDetail?.name_EN);
      this.myForm.get('nameTC')?.setValue(this.groupFilterDetail?.name_TC);
      this.myForm.get('nameSC')?.setValue(this.groupFilterDetail?.name_SC);
    }

    //get project ID
    this.projectID = localStorage.getItem('projectId');

    console.log('projectID: ' + this.projectID);

    //get system list (websocket)
    this.getSystemListFromWebsocket();

    //get update time
    this.getUpdateTime();
  }
  ngOnDestroy(): void {
    this.sharedService.isEditSystem = false;
    sessionStorage.removeItem('groupFormDetail');
  }

  showFailedDialog(errorCode: number) {
    if (errorCode == this.TIMEOUT) {
      this.eDialogRef = this.dialog.open(AddGroupFailDialogComponent, {
        width: '500px',
        data: 'timeout',
      });
    } else if (errorCode == this.SYSTEM_ERROR) {
      this.eDialogRef = this.dialog.open(AddGroupFailDialogComponent, {
        width: '500px',
        data: 'system_error',
      });
    } else {
      this.eDialogRef = this.dialog.open(AddGroupFailDialogComponent, {
        width: '500px',
        data: 'unknown_error',
      });
    }

    this.eDialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }

  changeSiteLanguage(localeCode: string): void {
    const selectedLanguage = this.languageList
      .find((language) => language.code === localeCode)
      ?.label.toString();

    if (selectedLanguage) {
      this.siteLanguage = selectedLanguage;
      this.translate.use(localeCode);
      console.log('localCode', localeCode);
      localStorage.setItem('LANG', localeCode);
    }
    setTimeout(() => {
      this.currentLanguage = this.translate.currentLang;
      console.log('currentLanguage', this.currentLanguage);
    }, 300);
  }

  openSubmittedDialog() {
    this.dialogRef = this.dialog.open(AddGroupFilterSubmitDialogComponent, {
      width: '450px',
    });

    this.dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
      if (result != null) {
        if (result['reset'] == true) {
          this.myForm.reset();
        } else if (result['path'] != null) {
          console.log('[group] previous url : ', this.previousUrl);
          if (this.previousUrl == '/equipment-setup') {
            // go back
            this.location.back();
          } else if (this.previousUrl == '/add-group-filter') {
            // Navigating to a new page ('/group-filter-list') and replacing the current page ('/add-group-filter')
            this.router.navigateByUrl(result['path'], { replaceUrl: true });
          } else if (this.previousUrl == '/admin') {
            // go back
            this.router.navigateByUrl(result['path'], { replaceUrl: true });
          } else {
            // go back
            this.location.back();
          }
        }
      }
    });
  }

  submitGroupFilter(options?: { id?: string; groupDetail?: GroupDetail }) {
    let action: string;

    let message: Message = {
      type: '',
      content: {},
    };

    message.type = 'group_DetailEditing';
    if (options.id !== undefined) {
      //edit
      action = 'edit';
      message.content = {
        action: 'edit',
        group_ID: options.id,
        username: this.userService.getUsername(),
        project_ID: this.projectID,
        update_time: this.updateTime,
        system_ID: options.groupDetail.system_ID,
        group: {
          name_EN: options.groupDetail.name_EN,
          name_CN: options.groupDetail.name_TC,
          name_SC: options.groupDetail.name_SC,
          designation: options.groupDetail.designation,
        },
      };
    } else {
      //create
      action = 'create';
      message.content = {
        action: 'create',
        username: this.userService.getUsername(),
        project_ID: this.projectID,
        update_time: this.updateTime,
        system_ID: options.groupDetail.system_ID,
        group: {
          name_EN: options.groupDetail.name_EN,
          name_CN: options.groupDetail.name_TC,
          name_SC: options.groupDetail.name_SC,
          designation: [],
        },
      };
    }

    console.log('group filter submit: ', message);

    this.websocketService.messages.next(message);

    if (this.isDev) {
      //Submit success
      if (
        this.myForm.get('sampleResponse').value == undefined ||
        this.myForm.get('sampleResponse').value == 'success'
      ) {
        this._sendSubmitGroupFilterResponse(action);
      }
      //submit fail with system error
      else if (this.myForm.get('sampleResponse').value == 'system_error') {
        this._sendSubmitFailResponse(this.SYSTEM_ERROR);
      }
      //submit fail with timeout
      else if (this.myForm.get('sampleResponse').value == 'timeout') {
        this._sendSubmitFailResponse(this.TIMEOUT);
      }
      //unknown error
      else {
        this._sendSubmitFailResponse(9999);
      }
    }
  }
  _sendSubmitGroupFilterResponse(action: string) {
    let message: Message = {
      type: '',
      content: {},
    };

    message.type = 'group_DetailEditing_Result';
    message.content = {
      action: action,
      username: this.userService.getUsername(),
      result: 'Success',
      order: 1,
      system_ID: 'system-1',
    };

    this.websocketService.messages.next(message);
  }

  SYSTEM_ERROR = 1001;
  TIMEOUT = 1000;

  _sendSubmitFailResponse(errorCode: number) {
    let errorMessage;
    switch (errorCode) {
      case this.SYSTEM_ERROR:
        errorMessage = 'System Error!';
        break;
      case this.TIMEOUT:
        errorMessage = 'Timeout, please try again. (Timeout 5 Seconds)';
        break;
    }

    let message: Message = {
      type: '',
      content: {},
    };

    message.type = 'group_DetailEditing_Result';
    message.content = {
      action: 'create',
      username: 'usr',
      processId: 'id-1000',
      error_Code: errorCode,
      result: errorMessage,
    };

    this.websocketService.messages.next(message);
  }

  submitForm() {
    //save group filter inputs
    console.log(this.myForm.get('nameEN')?.value);
    console.log(this.myForm.get('nameTC')?.value);
    console.log(this.myForm.get('nameSC')?.value);

    const formDetail = {
      nameEN: this.myForm.get('nameEN')?.value,
      nameTC: this.myForm.get('nameTC')?.value,
      nameSC: this.myForm.get('nameSC')?.value,
      systemId: this.myForm.get('systemId')?.value,
    };

    sessionStorage.setItem('groupFormDetail', JSON.stringify(formDetail));

    //custom validation
    if (
      this.myForm.get('nameEN')?.value == '' ||
      this.myForm.get('nameEN')?.value == null
    ) {
      this.myForm.get('nameEN')?.setErrors({ required: true });
    }
    if (
      this.myForm.get('nameTC')?.value == '' ||
      this.myForm.get('nameTC')?.value == null
    ) {
      this.myForm.get('nameTC')?.setErrors({ required: true });
    }
    if (
      this.myForm.get('nameSC')?.value == '' ||
      this.myForm.get('nameSC')?.value == null
    ) {
      this.myForm.get('nameSC')?.setErrors({ required: true });
    }

    if (this.myForm.invalid) {
      //error thrown if fields are empty
      return;
    }

    if (this.groupFilterDetail == null) {
      //Add new group filter
      this.submittedDetail = new GroupDetail({
        group_ID: this.updateTime,
        system_ID: this.myForm.get('systemId')?.value,
        name_EN: this.myForm.get('nameEN')?.value,
        name_TC: this.myForm.get('nameTC')?.value,
        name_SC: this.myForm.get('nameSC')?.value,
      });

      //Submit new group filter
      this.submitGroupFilter({
        groupDetail: this.submittedDetail,
      });

      // this.groupFilterArray.push(newGroupFilter);
    } else {
      if (this.systemList != undefined && this.systemList.length > 0) {
        this.groupFilterDetailArray =
          GroupFilterUtils.getGroupsBySystemFromLocalStorage(
            this.systemList[0].systemID
          )?.groups;
      } else {
        this.groupFilterDetailArray = [];
      }
      
      //Edit existing group filter
      let currentGfIndex = this.groupFilterDetailArray.findIndex(
        (g) => g.group_ID === this.groupFilterDetail.group_ID
      );

      this.submittedDetail = new GroupDetail({
        group_ID: this.groupFilterDetail.group_ID,
        name_EN: this.myForm.get('nameEN')?.value,
        name_TC: this.myForm.get('nameTC')?.value,
        name_SC: this.myForm.get('nameSC')?.value,
        designation: this.groupFilterDetail.designation,
      });

      //submit existing group filter
      this.submitGroupFilter({
        id: this.groupFilterDetail.group_ID,
        groupDetail: this.groupFilterDetailArray[currentGfIndex],
      });
    }
  }

  getSystemPlaceholder() {
    if (this.systemList.length == 0) {
      return 'No Systems Available. Please add a System';
    } else {
      return 'Select System';
    }
  }

  removeGroupFilter(options?: { groupFilter: GroupDetail; projectID: string }) {
    let message: Message = {
      type: '',
      content: {},
    };

    this.submittedDetail = new GroupDetail({
      group_ID: this.groupFilterDetail.group_ID,
      name_EN: this.myForm.get('nameEN')?.value,
      name_TC: this.myForm.get('nameTC')?.value,
      name_SC: this.myForm.get('nameSC')?.value,
      designation: this.groupFilterDetail.designation,
    });

    message.type = 'group_DetailEditing';
    message.content = {
      action: 'remove',
      group_ID: options.groupFilter.group_ID,
      username: this.userService.getUsername(),
      project_ID: this.projectID,
      update_time: this.updateTime,
      system_ID: options.groupFilter.system_ID,
    };

    this.websocketService.messages.next(message);

    if (this.isDev) {
      this._sendSubmitGroupFilterResponse('remove');
    }
  }

  remove(selectedFilter: GroupDetail) {
    this.confirmDialogRef = this.dialog.open(
      ConfirmDeleteSystemDialogComponent,
      {
        width: '450px',
        data: 'group filter',
      }
    );

    this.confirmDialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
      if (result != null && result['confirm'] == true) {
        this.removeGroupFilter({
          groupFilter: selectedFilter,
          projectID: this.projectID,
        });
      }
    });
  }

  navigateTo(path: string) {
    this.router.navigateByUrl(path);
  }

  backToLastPage() {
    this.location.back();
  }
}
