import { environment } from "src/environments/environment";
import { Constants } from "../constants";
import { Message } from "../message";
import { WebsocketService } from "../service/websocket.service";

export class Project {
  processid?: string;
  username?: string;
  result?: string;
  detail?: ProjectDetail;

  constructor(options?: {
    processid?: string;
    username?: string;
    result?: string;
    detail?: ProjectDetail;
  }) {
    this.processid = options?.processid;
    this.username = options?.username;
    this.detail = options?.detail;
  }
}

export class ProjectDetail {
  username?: string;
  project_EN?: string;
  project_CN?: string;
  project_SC?: string;
  project_ID?: string;

  constructor(options?: {
    username?: string;
    project_EN?: string;
    project_CN?: string;
    project_SC?: string;
    project_ID?: string;
  }) {
    this.username = options?.username;
    this.project_EN = options?.project_EN;
    this.project_CN = options?.project_CN;
    this.project_SC = options?.project_SC;
    this.project_ID = options?.project_ID;
  }
}

export class ProjectUtils {
  static getProjectFromLocalStorage() {
    if (localStorage.getItem('project') == null) {
      localStorage.setItem('project', JSON.stringify(new ProjectDetail()));
    }
    return JSON.parse(localStorage.getItem('project'));
  } 

  static sendUpdateProjectResponse(websocketService: WebsocketService) {
    let responseMessage: Message = {
      type: '',
      content: {},
    };
    responseMessage.type = Constants.UPDATE_PROJECT_SETTING_RESULT;
    responseMessage.content = {
      projectID: "project1",
      result: 'Success',
      detail: "Updating the user list.",
    }
    websocketService.messages.next(responseMessage);
  }

  static sendUpdateProject(projectData: ProjectDetail, username: string, websocketService: WebsocketService) {
    //submit to websocket
    let responseMessage: Message = {
      type: '',
      content: {},
    };
    responseMessage.type = Constants.UPDATE_PROJECT_SETTING;
    responseMessage.content = {
      username: username,
      update_time: Math.round(Date.now() / 1000),
      project_EN: projectData.project_EN,
      project_CN: projectData.project_CN,
      project_SC: projectData.project_SC,
    }

    websocketService.messages.next(responseMessage);

    let isDev = !environment.production;
    if (isDev) {
      this.sendUpdateProjectResponse(websocketService);
    }
  }


  static saveProjectToLocalStorage(projectData: ProjectDetail) {
    localStorage.setItem("project", JSON.stringify(projectData));
  }


    // static getProjectListFromLocalStorage() {
    //    return JSON.parse(localStorage.getItem('project-list')) ?? [];  
    // }

    // static saveProjectListToLocalStorage(projectArray: Project[]) {
    //     localStorage.setItem('project-list', JSON.stringify(projectArray));
    // }
}