import { Injectable } from '@angular/core';
import { Message } from '../message';
import { User } from '../user';
import { WebsocketService } from './websocket.service';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  constructor() { }

  //login the user
  public signIn(webSocketService: WebsocketService, userData: User){

    console.log('username : ' + userData.username)
    console.log('password : ' + userData.password)

    let loginMessage: Message = {
      type: '',
      content: '',
    };
    loginMessage.type = "user_login";
    loginMessage.content = userData;
    webSocketService.messages.next(loginMessage)

    localStorage.setItem('ACCESS_TOKEN', "access_token");
  }

  //check if the user is logged in
  public isLoggedIn(){
    return localStorage.getItem('ACCESS_TOKEN') !== null;
  }

  //logout the user
  public logout(webSocketService: WebsocketService){
    let logoutMessage = {
      type: "user_logout"
    };
    webSocketService.messages.next(logoutMessage)

    localStorage.removeItem('ACCESS_TOKEN');
    localStorage.removeItem('loginInfo');
  }
}
