import { Component, Inject } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MatDialogRef, MatDialog } from '@angular/material/dialog';
import { Router, ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ConvertUtils } from 'src/app/convert-utils';

import { RouterExtService } from 'src/app/service/route.service';
import { SharedService } from 'src/app/service/shared.service';
import { Location } from '@angular/common';
import { AddProjectSubmitDialogComponent } from '../add-project-submit-dialog/add-project-submit-dialog.component';
import { ConfirmDeleteSystemDialogComponent } from 'src/app/system/confirm-delete-system-dialog/confirm-delete-system-dialog.component';
import { Project, ProjectDetail, ProjectUtils } from '../project';
import { UserService } from 'src/app/service/user.service';
import { WebsocketService } from 'src/app/service/websocket.service';
import { MessageUtils } from 'src/app/message';
import { Constants } from 'src/app/constants';

@Component({
  selector: 'app-change-project',
  templateUrl: './change-project.component.html',
  styleUrls: ['./change-project.component.css'],
})
export class ChangeProjectComponent {
  static route = 'change-project';
  edit = false;
  previousUrl = undefined;
  showMenu = true;
  siteLanguage = 'English';
  languageList = [
    { code: 'en', label: 'English' },
    { code: 'zh_HK', label: '繁體中文' },
    { code: 'zh_CN', label: '简体中文' },
  ];
  currentLanguage?: String;

  private dialogRef?: MatDialogRef<AddProjectSubmitDialogComponent>;

  myForm: FormGroup;
  project: ProjectDetail;
  newProject;

  convertToSimplifiedChinese() {
    const _nameTC = this.myForm.get('nameTC')?.value;
    this.myForm
      .get('nameSC')
      ?.setValue(ConvertUtils.convertToSimplifiedChinese(_nameTC));
  }

  constructor(
    private translate: TranslateService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private location: Location,
    private dialog: MatDialog,
    private routerExtService: RouterExtService,
    private sharedService: SharedService,
    private userService: UserService,
    @Inject(WebsocketService) private WebSocketService: WebsocketService
  ) {
    let savedLang = localStorage.getItem('LANG');
    if (savedLang != null) {
      this.siteLanguage = savedLang;
      translate.use(savedLang);
      this.currentLanguage = translate.currentLang;
      console.log('currentLanguage', this.currentLanguage);
    }

    WebSocketService.reconnect();
    console.log('++++++++ reconnect websocket +++++++++');
    WebSocketService.messages.subscribe((msg) => {
      console.log('[project] Response from websocket: ' + msg);

      if (MessageUtils.isJsonString(msg.toString())) {
        var jObject = JSON.parse(msg.toString());

        // {"type":"get_projectVerifyResult","content":{"projectID":"project1","result":"Success","detail":"Updating the user list."}}

        if (jObject.type == Constants.UPDATE_PROJECT_SETTING_RESULT) {
          if (jObject.content['result'] == 'Success') {
            let projectID = jObject.content['projectID'];

            //Save projectID for new / edit project
            localStorage.setItem("projectId", projectID);

            ProjectUtils.saveProjectToLocalStorage(this.newProject);

            //update successful
            this.openSubmittedDialog();
          } else {
            //update fail
            alert('Update project setting failed!');
          }
        }
      }
    });

    //set isEditSystem to true
    this.sharedService.isEditSystem = true;

    //get previous navigation
    this.routerExtService.getPreviousUrl().then((value) => {
      this.previousUrl = value;
      console.log('previousUrl', this.previousUrl);
    });

    //form validation
    this.myForm = new FormGroup({
      //check automatically but validate even changing fields
      // id: new FormControl(''),
      nameEN: new FormControl(''),
      nameTC: new FormControl(''),
      nameSC: new FormControl(''),
      // Add more form controls as needed
    });

    this.project = ProjectUtils.getProjectFromLocalStorage();
    //prefill the data
    if (this.project != null) {
      // this.myForm.get('id')?.setValue(this.project?.id);
      this.myForm.get('nameEN')?.setValue(this.project?.project_EN);
      this.myForm.get('nameTC')?.setValue(this.project?.project_CN);
      this.myForm.get('nameSC')?.setValue(this.project?.project_SC);
    }
  }
  ngOnDestroy(): void {
    this.sharedService.isEditSystem = false;
  }

  getProjectTitle(): string {
    if (this.project.project_EN != null) {
      return 'Modify Project';
    } else {
      return 'Create Project';
    }
  }

  changeSiteLanguage(localeCode: string): void {
    const selectedLanguage = this.languageList
      .find((language) => language.code === localeCode)
      ?.label.toString();

    if (selectedLanguage) {
      this.siteLanguage = selectedLanguage;
      this.translate.use(localeCode);
      console.log('localCode', localeCode);
      localStorage.setItem('LANG', localeCode);
    }
    setTimeout(() => {
      this.currentLanguage = this.translate.currentLang;
      console.log('currentLanguage', this.currentLanguage);
    }, 300);
  }

  openSubmittedDialog() {
    this.dialogRef = this.dialog.open(AddProjectSubmitDialogComponent, {
      width: '450px',
    });

    this.dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
      if (result != null) {
        if (result['reset'] == true) {
          this.myForm.reset();
        } else if (result['path'] != null) {
          this.backToLastPage();
        }
      }
    });
  }

  submitForm() {
    console.log(this.myForm.get('nameEN')?.value);
    console.log(this.myForm.get('nameTC')?.value);
    console.log(this.myForm.get('nameSC')?.value);

    //custom validation
    if (
      this.myForm.get('nameEN')?.value == '' ||
      this.myForm.get('nameEN')?.value == null
    ) {
      this.myForm.get('nameEN')?.setErrors({ required: true });
    }
    if (
      this.myForm.get('nameTC')?.value == '' ||
      this.myForm.get('nameTC')?.value == null
    ) {
      this.myForm.get('nameTC')?.setErrors({ required: true });
    }
    if (
      this.myForm.get('nameSC')?.value == '' ||
      this.myForm.get('nameSC')?.value == null
    ) {
      this.myForm.get('nameSC')?.setErrors({ required: true });
    }

    if (this.myForm.invalid) {
      //error thrown if fields are empty
      return;
    }

    this.newProject = new ProjectDetail({
      project_EN: this.myForm.get('nameEN')?.value,
      project_CN: this.myForm.get('nameTC')?.value,
      project_SC: this.myForm.get('nameSC')?.value,
    });

    ProjectUtils.sendUpdateProject(
      this.newProject,
      this.userService.getUsername(),
      this.WebSocketService
    );

    // this.location.back();
  }

  backToLastPage() {
    //close websocket
    console.log('******** close websocket *********');
    this.WebSocketService.close();

    this.location.back();
  }
}
