// book.service.ts

import { Injectable } from '@angular/core';

@Injectable({ providedIn: 'root' })
export class UserService {
  private userInfo?: User | undefined;
  private loginInfo?: LoginInfo | undefined;

    discardUserLogin() {
        this.userInfo = undefined;
        this.loginInfo = undefined;
    }

    getUsername() : string {
      return this.getUserInfo().name;
    }

    getUserInfo(){
      if (this.userInfo == undefined) {
        //save last login info
        let stored = localStorage.getItem('loginInfo');
        if (stored != undefined) {
          this.loginInfo = JSON.parse(stored!);
          this.userInfo = this.loginInfo.user;
        }
      }
      return this.userInfo;
    }

    getLoginInfo() {
      if (this.loginInfo == undefined) {
        //save last login info
        let stored = localStorage.getItem('loginInfo');
        if (stored != undefined) {
          this.loginInfo = JSON.parse(stored!);
        }
      }
      return this.loginInfo;
    }

  saveUserLogin({mUsername, mUserLevel, processId, authResult, loginID}: NamedParameters) {
    this.userInfo = {name: mUsername, level: mUserLevel};
    this.loginInfo = {user: this.userInfo, processId: processId, authResult: authResult, id: loginID};

    localStorage.setItem('loginInfo', JSON.stringify(this.loginInfo)); //persistant across tabs
    sessionStorage.setItem('loginInfo', JSON.stringify(this.loginInfo));
    sessionStorage.setItem('userInfo', JSON.stringify(this.userInfo));
  }

  saveLoginInfo(loginInfo: LoginInfo) {
    this.loginInfo = loginInfo;
  }

}

interface NamedParameters {
    mUsername: string;
    mUserLevel: number;
    processId: string;
    authResult: string;
    loginID: string;
}

export type User = {
    name: string;
    level: number;
}

export type LoginInfo = {
    user: User;
    processId: string;
    authResult: string;
    id: string;
}