import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-group-fail-dialog',
  templateUrl: './add-equipment-submit-dialog.component.html',
  styleUrls: ['./add-equipment-submit-dialog.component.css'],
})
export class AddEquipmentSubmitDialogComponent {
  constructor(
    private router: Router,
    private dialogRef: MatDialogRef<AddEquipmentSubmitDialogComponent>
  ) {}

  addNew() {
    this.dialogRef.close({ reset: true }); //close dialog
  }

  navigateTo(path: string) {
    this.dialogRef.close({'path':path}); //close dialog
    // Navigating to a new page and replacing the current page
    // this.router.navigateByUrl(path, { replaceUrl: true });
  }
}
