  toggleMoreOrLessGroupFilters() {
    this.isExpanded = !this.isExpanded;

    if (this.isExpanded) {
      this.groupDetailData = this.groupFiltersDesc;
    } else {
      this.groupDetailData = this.groupFiltersDesc.slice(0, 3);
      this.scrollToTop();
    }
  }

  showMoreOrLessGroupFilters() {
    if (this.isExpanded) {
      return this.translate.instant('show_less');
    } else {
      return this.translate.instant('show_more');
    }
  }

  logout() {
    this.userService.discardUserLogin();
    this.authService.logout(this.WebSocketService);

    this.closeWebsocket('/auth');
    // this.router.navigateByUrl('/auth');
  }

  showDeviceDialog(device: DeviceData, sysData: SystemData, sysKey: string) {
    this.selected = device;

    this.dialogRef = this.dialog.open(ShowDeviceDialogComponent, {
      panelClass: 'show-device',
      data: new SystemDeviceData(device, sysData, sysKey),
      width: '50%',
      minWidth: '500px',
      autoFocus: false,
    });

    this.dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
      if (result.save) {
        //Remind user
        this.dialog.open(RemindUserDialogComponent);
      }
    });
  }

