import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthComponent } from './auth/auth.component';
import { AdminComponent } from './admin/admin.component';
import { AuthGuard } from './service/auth.guard';
import { FilterDeviceLogComponent } from './preview/filter-device-log/filter-device-log.component';
import { ViewDownloadReportComponent } from './preview/view-download-report/view-download-report.component';
import { PreviewComponent } from './preview/preview.component';
import { AddEquipmentComponent } from './system/add-equipment/add-equipment.component';
import { EquipmentListComponent } from './system/equipment-list/equipment-list.component';
import { SystemListComponent } from './system/system-list/system-list.component';
import { AddSystemComponent } from './system/add-system/add-system.component';
import { AddGroupFilterComponent } from './system/group-filters/add-group-filter/add-group-filter.component';
import { GroupFilterListComponent } from './system/group-filters/group-filter-list/group-filter-list.component';
import { ChangeProjectComponent } from './project/change-project/change-project.component';

const routes: Routes = [
  { path: '', pathMatch: 'full', redirectTo: 'admin' },
  { path: 'auth', component: AuthComponent },
  { path: 'admin', component: AdminComponent, canActivate: [AuthGuard] },
  { path: FilterDeviceLogComponent.route, component: FilterDeviceLogComponent, canActivate: [AuthGuard] },
  { path: ViewDownloadReportComponent.route, component: ViewDownloadReportComponent, canActivate: [AuthGuard]},
  { path: PreviewComponent.route, component: PreviewComponent, canActivate: [AuthGuard]},
  { path: AddEquipmentComponent.route, component: AddEquipmentComponent, canActivate: [AuthGuard]},
  { path: EquipmentListComponent.route, component: EquipmentListComponent, canActivate: [AuthGuard]},
  { path: SystemListComponent.route, component: SystemListComponent, canActivate: [AuthGuard]},
  { path: AddSystemComponent.route, component: AddSystemComponent, canActivate: [AuthGuard]},
  { path: AddGroupFilterComponent.route, component: AddGroupFilterComponent, canActivate: [AuthGuard]},
  { path: GroupFilterListComponent.route, component: GroupFilterListComponent, canActivate: [AuthGuard]},
  { path: ChangeProjectComponent.route, component: ChangeProjectComponent, canActivate: [AuthGuard]},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }

