import { Equipment, EquipmentSetting, SystemDetail } from "../equipment-setting";
import { LampData } from "./add-equipment/lamp-data";
import { SystemUtils } from "./system-utils";

export class EquipmentUtils {

    //1. get equipment setting
    static getEquipmentSettingToLocalStorage() : EquipmentSetting {
      let setting = new EquipmentSetting();
      let parse = JSON.parse(localStorage.getItem('equipment-setting'));
      return Object.assign(setting, parse);
    }

    //2. save equipment setting
    static saveEquipmentSettingToLocalStorage(setting: EquipmentSetting) {
      localStorage.setItem('equipment-setting', JSON.stringify(setting));
    }

    //3. get equipment list *make sure you have saved equipment-setting
    static getEquipmentBySystemId(systemId: string) {
      let settingStr = localStorage.getItem('equipment-setting');
      if (settingStr != null) {
        let setting = JSON.parse(settingStr) as EquipmentSetting;
        let systemDetail = setting.detail.find(e => e.systemID === systemId) as SystemDetail; //find the first system matches systemId
        if (systemDetail) {
          let equipmentList = systemDetail.equipments;
          return equipmentList;
        } else {
          return [];
        }
        
      }
      return [];
    }

    //4. save equipment to equipment setting
    static saveEquipmentToSetting(equipment: Equipment, systemId: string) {
      let setting = EquipmentUtils.getEquipmentSettingToLocalStorage();
      let systemIndex = setting.detail.findIndex(e => e.systemID === systemId);

      if (systemIndex != -1) {
        //new equipment added to the equipment list
        setting.detail[systemIndex].equipments ??= [];
        setting.detail[systemIndex].equipments.push(equipment);
      } else {
        //create and insert a new system detail
        let systemDetail : SystemDetail = SystemUtils.getListFromLocalStorage().find(e => e.systemID === systemId);
        systemDetail.equipments ??= [];
        systemDetail.equipments.push(equipment);
        setting.detail.push(systemDetail);
      }

      localStorage.setItem('equipment-setting', JSON.stringify(setting));
    }

    //4aa. save equipment list to equipment setting
    static saveEquipmentListToLocalStorage(equipmentList: Equipment[], systemId: string) {
      let setting = EquipmentUtils.getEquipmentSettingToLocalStorage();
      let systemIndex = setting.detail.findIndex(e => e.systemID === systemId);
      if (systemIndex != -1) {
        //new equipment added to the equipment list
        setting.detail[systemIndex].equipments = equipmentList;
      } else {
        //create and insert a new system detail
        let systemDetail : SystemDetail = SystemUtils.getListFromLocalStorage().find(e => e.systemID === systemId);
        systemDetail.equipments = equipmentList;
        systemDetail.systemID = systemId;
        setting.detail.push(systemDetail);
      }

      localStorage.setItem('equipment-setting', JSON.stringify(setting));
    }

    //4a. save edited equipment from setting
    static editEquipmentToSetting(equipment: Equipment, systemId: string) {
      let setting = EquipmentUtils.getEquipmentSettingToLocalStorage();
      let equipments = EquipmentUtils.getEquipmentBySystemId(systemId);

      //remove the selected object in equipment list
      const equipmentIndex = equipments.findIndex(
        (e) => e.equipment_ID === equipment.equipment_ID
      );
      equipments[equipmentIndex] = equipment;

      //update equipment setting
      let systemIndex = setting.detail.findIndex(e => e.systemID === systemId);
      setting.detail[systemIndex].equipments = equipments;

      localStorage.setItem('equipment-setting', JSON.stringify(setting));
    }

    //5. remove equipment from equipment setting
    static removeEquipmentFromSetting(equipmentId: string, systemId: string) {
      let setting = EquipmentUtils.getEquipmentSettingToLocalStorage();
      let equipments = EquipmentUtils.getEquipmentBySystemId(systemId);

      //remove the selected object in equipment list
      const equipmentIndex = equipments.findIndex(
        (e) => e.equipment_ID === equipmentId
      );
      equipments.splice(equipmentIndex, 1);

      //update equipment setting
      let systemIndex = setting.detail.findIndex(e => e.systemID === systemId);
      setting.detail[systemIndex].equipments = equipments;

      localStorage.setItem('equipment-setting', JSON.stringify(setting));
    }

    static saveLampDataListToLocalStorage(lampDataList) {
        localStorage.setItem('lamp-data', JSON.stringify(lampDataList));
    }

    static getLampDataListFromLocalStorage() : LampData[] {
      let retString = localStorage.getItem('lamp-data');
      let r = JSON.parse(retString); //json array
      let lampDataList = [];

      r.forEach((lamp) => {
        let lampData = LampData.fromJSON(lamp);

        lampDataList.push(lampData);

        console.log("nameEN : ",lampData.nameEN)
      });

      return lampDataList;
    }

    static reassignOrder(srcList: Equipment[]) {
        let result = [];
        for (let i = 0; i < srcList.length; i++) {
          srcList[i].order = i + 1;
            result.push(srcList[i]);
        }
        return result;
    }
}