import { DeviceData } from "./device-data";

//For Web Portal, Preview Use
export class SystemData {
    public deivceData?: DeviceData[];
    public color?: string;
    public alarm?: string;
    public systemName_EN?: string;
    public systemName_CN?: string;
    public systemName_SC?: string;
    public numberOfDevice?: number;
    public numberDeviceAlarm?: number;

    constructor(data: DeviceData[], color?: string, alarm?: string, systemNameEN?:string, systemNameCN?:string, systemNameSC?:string, numOfDevs?:number, numOfAlarms?:number) {
        this.deivceData = data;
        this.color = color;
        this.alarm = alarm;
        this.systemName_EN = systemNameEN;
        this.systemName_CN = systemNameCN;
        this.systemName_SC = systemNameSC;
        this.numberOfDevice = numOfDevs;
        this.numberDeviceAlarm = numOfAlarms;
    }
}
