export class GroupDetail {
    group_ID?: string
    name_EN?: string
    name_TC?: string
    name_SC?: string
    designation?: string[]
    system_ID?: string

    constructor(options?: {
        group_ID?: string
        name_EN?: string
        name_TC?: string
        name_SC?: string
        designation?: string[]
        system_ID?: string //defined this in the beginning
    }) {
        this.group_ID = options?.group_ID
        this.name_EN = options?.name_EN
        this.name_TC = options?.name_TC
        this.name_SC = options?.name_SC
        this.designation = options?.designation
        this.system_ID = options?.system_ID
    }
}