  extractDesignationFromCachedString(cachedString: string) {
    // let designationLogs : DeviceLogContent[];
    // this.deviceLogs = new DeviceLog();
    this.designationLogs = new DeviceLog()

    if (cachedString) {
      let deviceLogJSON = JSON.parse(cachedString)
      this.designationLogs.processId = deviceLogJSON.processId
      this.designationLogs.content = deviceLogJSON.content
      const over1000 = deviceLogJSON.over1000
      /**
       * Show Designation Log Datas to the table layout
       */
      this.dataSource = new MatTableDataSource(this.designationLogs.content)

      if (over1000) {
        this.showOver1000Popup();
      }
    }
  }

  saveDesignationLogToMemory(designationId: string, designation: DeviceLog) {
    localStorage.setItem("desiLog_" + designationId, JSON.stringify(designation));
    console.log("[cache] created : ", "desiLog_" + designationId)
  }

  filterDesignationsFromLog(designationId: string) {
    let filteredLog = this.deviceLogs.content.filter((element, index, array) => {
      return (element.designation == designationId)
    })

    let desiLog = new DeviceLog();
    desiLog.processId = new Date().getMilliseconds(); //set a arbitrary number
    desiLog.content = filteredLog; //filtered result

    this.saveDesignationLogToMemory(designationId, desiLog) //store in local storage for access

    console.log("designation : ", designationId, "filtered Log size : ", filteredLog.length)
  }

  sendDesignationRequest(designationId: string) {
    this.errorMessage = undefined;
    // this.deviceLogs = undefined;

    let queryMessage: Message = {
      type: '',
      content: {},
    }

    let _startDate = this.formatStartDate(this.start);
    let _endDate = this.formatEndDate(this.end);

    console.log("send designation request", designationId)

    if (!this.isDev) {
      queryMessage.type = "get_DesignationLogs";
      queryMessage.content = {
        "Time": {
          "start": _startDate,
          "end": _endDate,
        },
        "Designation": [designationId],
      }

      this.WebSocketService.messages.next(queryMessage);
    }
    else {
      //Get Sample Designation Log Directly
      this.getSampleDesignationLog(designationId);
    }
  }

