  formatStartDate(start: any) {
    let date = new Date(start);
    date.setHours(0, 0, 0, 0);

    //change date to unixtime
    return new Date(date).getTime();
  }

  formatEndDate(end: any) {
    let date = new Date(end);
    date.setHours(23, 59, 59, 0);

    //change date to unixtime
    return new Date(date).getTime();
  }

  clearAllCaches() {
    this.cacheDesignationContents = [];

    Object.keys(localStorage).filter((key) => {
      return key.startsWith("desiLog_")
    }).map((key) => {
      console.log("[cache] clear : ", key)
      localStorage.removeItem(key);
    })
  }

  //Send Filter Device Log Query
  submit() {
    if (this.isSubmitting) {
      return;
    }

    this.isSubmitting = true;

    //Clear all designation caches
    this.clearAllCaches()

    setTimeout(() => {
      if (this.isSubmitting)
        this.isSubmitting = false;
    }, 1500);

    this.errorMessage = undefined;
    this.deviceLogs = undefined;
    // console.log(this.form.value);

    let queryMessage: Message = {
      type: '',
      content: {},
    }

    let _startDate = this.formatStartDate(this.start);
    let _endDate = this.formatEndDate(this.end);

    if (_endDate > _startDate) {
      if (_startDate < 0 || _endDate < 60000000) {
        console.log("invalid_date");
        this.errorMessage = this.translate.instant("err_invalid_date")
        this.showPopup();
      } else if (_endDate - _startDate > 2764799000) {
        // more than 31 days
        console.log("diff startDate & endDate", _endDate - _startDate)
        this.errorMessage = this.translate.instant("err_more_than_1_mo")
        this.showPopup()
      } else {
        queryMessage.type = "get_DeviceLogs";
        queryMessage.content = {
          "Time": {
            "start": _startDate,
            "end": _endDate,
          },
          "System": this.retrieveSystemValue(),
          "Floor": this.retrieveFloorValue(),
          "Type": this.retrieveTypeValue(),
        }

        this.WebSocketService.messages.next(queryMessage);
      }
    } else {
      console.log("Start date after end date");
      this.errorMessage = this.translate.instant("err_invalid_date")
      this.showPopup();
    }
  }

  retrieveSystemValue() {
    const sysList = this.form.get('selectedSystems')?.value;
    let sysNames = [];

    if (sysList !== undefined && sysList !== null) {
      for (let single of sysList) {
        sysNames.push(single.key)
      }
    }
    return sysNames;
  }

  retrieveFloorValue() {
    const fList = this.form.get('selectedFloors')?.value;
    let floorNames = [];

    if (fList !== undefined && fList !== null) {
      for (let single of fList) {
        floorNames.push(single.key)
      }
    }
    return floorNames;
  }

  retrieveTypeValue() {
    const typeList = this.form.get('selectedTypes')?.value;
    let typeNames = [];

    if (typeList !== undefined && typeList !== null) {
      for (let single of typeList) {
        typeNames.push(single.key)
      }
    }
    return typeNames;
  }

