  ngOnInit(): void {
    this.browserRefresh = browserRefresh;
    console.log('refreshed?:', browserRefresh);

    //     var a = document.getElementsByTagName('a');
    // for(let i=0 ; i<a.length ; i++){
    //   a[i].addEventListener('click', function(e) {
    //     if (this.href === window.location.href) {
    //     		console.log("Foo click");
    //         e.preventDefault();
    //     }
    // });
    // }
    this.getSystemDropdownSettings();
    this.getFloorDropdownSettings();
    this.getTypeDropdownSettings();

    this._getUserInfo();

    if (this.showMenu == false) {
      setTimeout(() => this.showMenu = true, 2000);
    }

    setTimeout(() => {
      if (browserRefresh) {
        let stored = sessionStorage.getItem('loginInfo');
        if (stored !== undefined) {
          console.log("execute webSocket_Connected after refresh [filter]")
          let loginInfo: LoginInfo = JSON.parse(stored!);
          this.userService.saveLoginInfo(loginInfo);
          this.sendWSConnected();
        }
        // this.sendWSOnClose(browserRefresh);
      } else {
        if (this.userService.getLoginInfo() != undefined) {
          console.log("execute webSocket_Connected after 1 sec [filter]")
          this.sendWSConnected()
        }
        //wait for a second
      }
    }, 1000)

    window.addEventListener('beforeunload', async () => {
      //call when user clicked refresh
      if (!AppComponent.isCallingOnClose) {
        console.log("Message onclose (filter)")
        this.sendWSOnClose();
      }
    }, false);

    window.addEventListener('popstate', () => {
      //back button clicked
      if (!AppComponent.isCallingOnClose) {
        AppComponent.isCallingOnClose = true;
        this.closeWebsocket();
      }
    }, false);

  }

  closeWebsocket(navigateTo?: any) {
    // if (!this.WebSocketService.isClosed()) {
    this.sendWSOnClose() //TODO: is this required anymore?
    console.log("******** close websocket *********")
    this.WebSocketService.close()
    // }

    if (navigateTo) {
      if (typeof (navigateTo) == 'string') {
        this.router.navigateByUrl(navigateTo)
      } else {
        this.router.navigate(navigateTo)
      }
    }
  }

  sendWSOnClose(navigateTo?: any) {
    this.browserRefresh = !browserRefresh;
    AppComponent.isCallingOnClose = true;

    let queryMessage: Message = {
      type: '',
      content: {},
    };
    queryMessage.type = "CloseEvent";
    queryMessage.content = {
      "F5Refresh": ''
    }

    this.WebSocketService.messages.next(queryMessage)

    if (navigateTo) {
      if (typeof (navigateTo) == 'string') {
        this.router.navigateByUrl(navigateTo)
      } else {
        this.router.navigate(navigateTo)
      }
    }
  }

