  exportCSV(fileName?: string, designationId?: string) {
    this.exportCSVName = fileName;
    let queryMessage: Message = {
      type: '',
      content: {},
    }
    let _startDate = this.formatStartDate(this.start);
    let _endDate = this.formatEndDate(this.end);
    queryMessage.type = "get_DeviceFullLogs";
    if (!this.isDev) {
      if (this.designationHeader == undefined) {
        queryMessage.content = {
          "Time": {
            "start": _startDate,
            "end": _endDate,
          },
          "System": this.retrieveSystemValue(),
          "Floor": this.retrieveFloorValue(),
          "Type": this.retrieveTypeValue(),
        }
      } else {
        queryMessage.content = {
          "Time": {
            "start": _startDate,
            "end": _endDate,
          },
          "Designation": [designationId],
        }
      }
      this.WebSocketService.messages.next(queryMessage);
    } else {
      //test export csv
      if (this.designationHeader == undefined) {
        this.WebSocketService.messages.next(Constants.SAMPLE_EXPORT_CSV_LOG);
      } else {
        this.WebSocketService.messages.next(Constants.SAMPLE_EXPORT_CSV_DESIGNATION);
      }
    }
  }

