  getLampName(lamp: Language, lampKey: string) {
    switch (this.currentLanguage) {
      case "zh_HK":
        return lamp.langTC == null ? lampKey : lamp.langTC;
      case "zh_CN":
        return lamp.langSC == null ? lampKey : lamp.langSC;
      default:
        return lamp.langEN == null ? lampKey : lamp.langEN;
    }
  }

