interface NamedArguments {
  now: boolean; // true if you want to get exact time 7 days ago from now
}

function isJsonString(str: string) {
  try {
    JSON.parse(str);
  } catch (e) {
    return false;
  }
  return true;
}

export class SystemName {
  public key?: string;
  public nameEN?: string;
  public nameSC?: string;
  public nameCN?: string;

  constructor(k: string, en: string, sc: string, cn: string) {
    this.key = k;
    this.nameEN = en;
    this.nameSC = sc;
    this.nameCN = cn;
  }
}

export class FilterFloor {
  public key?: string;
  public name?: string;

  constructor(k: string, n: string) {
    this.key = k;
    this.name = n;
  }
}


export class LogType {
  public key?: string;
  public nameEN?: string;
  public nameSC?: string;
  public nameCN?: string;

  constructor(k: string, en: string, sc: string, cn: string) {
    this.key = k;
    this.nameEN = en;
    this.nameSC = sc;
    this.nameCN = cn;
  }
}