import { Component } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Location } from '@angular/common';


@Component({
  selector: 'app-upload-summary',
  templateUrl: './upload-summary.component.html',
  styleUrls: ['./upload-summary.component.css']
})
export class UploadSummaryComponent {
  siteLanguage = 'English';
  languageList = [
    { code: 'en', label: 'English' },
    { code: 'zh_HK', label: '繁體中文' },
    { code: 'zh_CN', label: '简体中文' },
  ];
  currentLanguage?: String;
  showMenu = true;

  constructor(private translate: TranslateService, private location: Location) {
    let savedLang = localStorage.getItem('LANG');
    if (savedLang != null) {
      this.siteLanguage = savedLang;
      translate.use(savedLang);
      this.currentLanguage = translate.currentLang;
      console.log('currentLanguage', this.currentLanguage);
    }
  }

  changeSiteLanguage(localeCode: string): void {
    const selectedLanguage = this.languageList
      .find((language) => language.code === localeCode)
      ?.label.toString();

    if (selectedLanguage) {
      this.siteLanguage = selectedLanguage;
      this.translate.use(localeCode);
      console.log('localCode', localeCode);
      localStorage.setItem('LANG', localeCode);
    }
    setTimeout(() => {
      this.currentLanguage = this.translate.currentLang;
      console.log('currentLanguage', this.currentLanguage);
    }, 300);
  }

  backToLastPage() {
    this.location.back();
  }

  goToUploadSummary() {
    alert("Go to Upload Summary")
  }
}
