import { Component, ElementRef, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Message } from '../message';
import { SystemData } from '../system-data';
import { DeviceData } from '../device-data';
import { MatDialog, MatDialogModule, MatDialogRef } from "@angular/material/dialog";
import { ShowDeviceDialogComponent, SystemDeviceData } from './show-device-dialog/show-device-dialog.component';
import { RemindUserDialogComponent } from './remind-user-dialog/remind-user-dialog.component';
import { environment } from 'src/environments/environment';
import { Constants } from '../constants';
import { WebsocketService } from '../service/websocket.service';
import { LoginInfo, UserService } from '../service/user.service';
import { AuthService } from '../service/auth.service';
import { ViewDownloadReportComponent } from './view-download-report/view-download-report.component';
import { FilterDeviceLogComponent } from './filter-device-log/filter-device-log.component';
import { AppComponent, browserRefresh } from '../app.component';
import { KeyValue } from '@angular/common';
import { TranslateService } from '@ngx-translate/core';
import { MatTableDataSource } from '@angular/material/table';
import { RemindLoginDialogComponent } from '../remind-login-dialog/remind-login-dialog.component';
import { AdminComponent } from '../admin/admin.component';

@Component({
  selector: 'app-preview',
  templateUrl: './preview.component.html',
  styleUrls: ['./preview.component.css']
})
export class PreviewComponent implements OnInit, OnDestroy {
  static route = "preview";

  show: boolean = true;

  systemDev?: Map<string, SystemData> = new Map(); //for update UI (clone from systemMap)
  systemMap?: Map<string, Object> = new Map(); //for storing deviceData for next update

  systemNames?: string[];
  systemData?: any[];
  alarmStatus?: SystemData;

  dateTime?: string;
  isExpanded?: boolean;
  selected?: DeviceData;

  //environment configuration (refer to /environment)
  isDev: boolean = false;

  isAdmin: boolean = false;
  username?: String;
  browserRefresh = false;

  showMenu = false;
  sticky?: number;
  header?: HTMLElement;
  colors = ['red', 'yellow', 'yellowgreen', 'blue', 'violet'];
  stickyWidth?: number;
  windowScrolled = false;
  windowWidth = 0;

  siteLanguage = 'English';
  languageList = [
    {code: 'en', label: 'English'},
    {code: 'zh_HK', label: '繁體中文'},
    {code: 'zh_CN', label: '简体中文'}
  ];
  currentLanguage?: String;

  @ViewChild('header', {static: true}) _header?: ElementRef;

  private dialogRef?: MatDialogRef<ShowDeviceDialogComponent>
  displayedColumns: string[] = ['startTime', 'content'];

  dataSource: any;

  public timer;
  private remindLoginRef?: MatDialogRef<RemindLoginDialogComponent>

  localServerStatus: boolean = undefined;

  // buildingNameEN: string;
  // buildingNameSC: string;
  // buildingNameCN: string;
  buildingNames: {};

  name= "Charlie";

  getWindowWidth() {
    console.log("window width", window.innerWidth)
    if (window.innerWidth > 1000) {
      this.windowWidth = window.innerWidth;
    } else {
      this.windowWidth = 1000;
    }
    
  }

  constructor(@Inject(WebsocketService) private WebSocketService: WebsocketService,
    private userService: UserService,
    private authService: AuthService,
    private router: Router,
    private modalService: NgbModal,
    private dialog: MatDialog,
    private translate: TranslateService) {

    let savedLang = localStorage.getItem("LANG");
    if (savedLang != null) {
      this.siteLanguage = savedLang;
      translate.use(savedLang);
      this.currentLanguage = translate.currentLang;
      console.log("currentLanguage",this.currentLanguage);
    }

    console.log('production? : ' + environment.production);
    this.isDev = !environment.production;

    WebSocketService.reconnect()
    console.log("++++++++ reconnect websocket ++++++++")
    WebSocketService.messages.subscribe(msg => {
      console.log("[admin] Response from websocket: " + msg);

      if (!this.isDev) {
        this.updateSessionTimer()
      }

      if (AppComponent.isCallingOnClose) {
        //reset
        // setTimeout(() => {this.isCallingOnClose = false;},200);
        AppComponent.isCallingOnClose = false;
      }

      if (isJsonString(msg.toString())) {
        var jObject = JSON.parse(msg.toString());
        let timeNow = new Date();
        const result = timeNow.toLocaleTimeString("en-GB", { // you can use undefined as first argument
          hour: "2-digit",
          minute: "2-digit",
          second: "2-digit",
        })
        this.dateTime = result;
        // this.dateTime = timeNow.getHours() + ":" + timeNow.getMinutes() + ":" + timeNow.getSeconds();

        //Check if it is DEVICE DATA
        if (jObject.type.endsWith('DeviceData')) {
          
          // Get the header
          this.header = document.getElementById("header")!;
          
          let systemName = jObject.type.replace("get_", "").replace("_DeviceData", "");
          if (systemName !== "AlarmStatus") {
            //SystemDeviceData
            this.systemMap!.set(systemName, jObject.content);

            if (this.dialogRef && this.dialogRef.componentInstance) {
              if (this.dialogRef.componentInstance.system.systemKey === systemName) {
                for (let object of jObject.content['DeviceData']) {
                  let _devData = new DeviceData(object);

                  if (_devData.designation === this.dialogRef.componentInstance.system.deviceData.designation) {
                    console.log("Found device : " + _devData.nameEN);
                    this.dialogRef.componentInstance.system.deviceData = _devData;
                  }
                }
              }
            }
          } else {
            //AlarmStatus
            let object = jObject.content['DeviceData']
            let devAlarms: DeviceData[] = [];
            for (let obj of object) {
              let alarm = new DeviceData(obj);
              devAlarms.push(alarm)
            }

            let color = jObject.content['Color']
            let alarmFlag = jObject.content['Alarm']
            let systemName_EN = jObject.content['System_Name_EN']
            let systemName_CN = jObject.content['System_Name_CN']
            let systemName_SC = jObject.content['System_Name_SC']
            let numberOfDevice = jObject.content['Number_Of_Device']
            let numberDeviceAlarm = jObject.content['Number_Device_Alarm']

            // console.log("[Alarm] systemNameEN : ", systemName_EN)
            // console.log("[Alarm] systemNameCN : ", systemName_CN)
            // console.log("[Alarm] systemNameSC : ", systemName_SC)
            // console.log("[Alarm] numberOfDevice : ", numberOfDevice)
            // console.log("[Alarm] numberDeviceAlarm : ", numberDeviceAlarm)

            this.alarmStatus = new SystemData(devAlarms, color, alarmFlag, systemName_EN, systemName_CN, systemName_SC, numberOfDevice, numberDeviceAlarm);

            if (this.isExpanded) {
              this.dataSource = new MatTableDataSource(devAlarms);
            } else {
              this.dataSource = new MatTableDataSource(devAlarms.slice(0,3));
            }
          }

        } else if (jObject.type.endsWith("filterLogLists")) {
            //get_FilterLogLists
            // console.log('[SystemMap] ' + this.systemMap!.size);
            let deviceList = jObject.content.deviceList;
            console.log(`new deviceList : ${deviceList}`);

            for (let systemName of deviceList) {
              if (this.systemMap != null && this.systemMap.has(systemName)) {
                //Add new system entry if not exists; Update if available
                let objectMap = this.systemMap.get(systemName)!;
                // let sysData = new SystemData();
                let devices: DeviceData[] = [];
                let color = undefined;
                let alarm = undefined;
                let systemName_EN : string = "";
                let systemName_CN : string = "";
                let systemName_SC : string = "";

                for (let [key, object] of Object.entries(objectMap)) {
                  if (key == 'DeviceData') {
                    // sysData.deivceData = [];
                    for (let obj of object) {
                      let deviceData = new DeviceData(obj);
                      devices.push(deviceData);
                    }
                  } else if (key == 'Color') {
                    color = object
                  } else if (key == 'Alarm') {
                    alarm = object
                  } else if (key == 'System_Name_EN') {
                    systemName_EN = object
                  } else if (key == 'System_Name_CN') {
                    systemName_CN = object
                  } else {
                    systemName_SC = object
                  }
                }
                //  let systemName_EN = data.systemName_EN
                //  let systemName_CN = data.systemName_CN
                //  let systemName_SC = data.systemName_SC
                let sysData = new SystemData(devices, color, alarm, systemName_EN, systemName_CN, systemName_SC);
                // let sysData = new SystemData(devices, color);
                this.systemDev!.set(systemName, sysData);
              }
            }
        } else if (jObject.type.endsWith("LocalServerStatus")) {
          console.log("localServerStatus : ", jObject.content["localServerStatus"]);
          this.localServerStatus = jObject.content["localServerStatus"];
        } else if (jObject.type.endsWith("BuildingName")) {
          console.log("[WS]BuildingName : ", jObject.content);
          if (jObject.content) {
            const stringify = JSON.stringify(jObject.content)
            this.buildingNames = stringify
            localStorage.setItem("buildinName", stringify);
          }
        }
      }

    });

    /*addEventListener("resize", (event) => {
      this.getWindowWidth();
    });*/
  }

  updateAlignment(numOfSystems:number) {
    if (numOfSystems != undefined && numOfSystems > 5) {
      return '0px';
    } else {
      return 'auto';
    }
  }

  ngOnDestroy(): void {
    if (this.timer) {
      clearTimeout(this.timer)
    }
  }

  updateSessionTimer() {
    if (this.timer !== null) {
      clearTimeout(this.timer)
      // console.warn("clear 10 seconds timeout")
    }
    // console.error("set 10 seconds timeout")
    this.timer = setTimeout(() => {
      console.log("60 seconds over")
      this.showSessionTimeoutDialog()
    }, 60000);
  }

  showSessionTimeoutDialog() {
    this.remindLoginRef = this.dialog.open(RemindLoginDialogComponent, { panelClass: 'show-device'});

    this.remindLoginRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
      if (!result || result == undefined) {
        this.updateSessionTimer()
      } else {
        this.logout()
      }
    });
  }

  originalOrder = (a: KeyValue<string,Object>, b: KeyValue<string,Object>): number => {
    return 0;
  }

  ngOnInit() { 
    this.browserRefresh = browserRefresh;
    console.log('refreshed?:', browserRefresh);

    this._getUserInfo();

    if (this.showMenu == false) {
      setTimeout(() => {
        this.showMenu = true;
      }, 2000);
    }

    setTimeout (() => {
      if (browserRefresh) {
        let stored = sessionStorage.getItem('loginInfo');
        if (stored !== undefined) {
          console.log("execute webSocket_Connected after refresh [admin]")
          let loginInfo : LoginInfo = JSON.parse(stored!);
          this.userService.saveLoginInfo(loginInfo);
          this.sendWSConnected();
        }
        // this.sendWSOnClose(browserRefresh);
      } else {
        if (this.userService.getLoginInfo() != undefined) {
          console.log("execute webSocket_Connected after 1 sec [admin]")
          this.sendWSConnected()
        }
        //wait for a second
      }
    }, 1000)

    window.addEventListener('beforeunload', () => {
      //call when user clicked refresh
      if (!AppComponent.isCallingOnClose) {
        AppComponent.isCallingOnClose = true;

        console.log("Message onClose (admin)"); 
        //TODO: can't differentiate between refresh and close window
        //this is admin
        this.sendWSOnClose();
      }
    } , false);

    window.addEventListener('popstate', () => {
      //back button clicked
      if (!AppComponent.isCallingOnClose) {
        AppComponent.isCallingOnClose = true;
        this.closeWebsocket();
      }
    }, false);
  }

  // getPosition(event: any) {
  //   let element = document.querySelector("#target")
  //   let position = element.getBoundingClientRect().top;
  //   console.log("getPosition", position)
  // }

  scrollToTop(): void {
    // scroll to the top of the body
    return document.body.scrollIntoView({
      behavior: 'smooth',
      block: 'start',
      inline: 'start',
    });
  }

  onScroll(e:Event) {
    let scrollExtent = (e.target as HTMLElement).scrollLeft
    // console.log(scrollExtent)

    //make system header and content scroll in the same extent
    let content = document.getElementById("content")!
    content.scrollTo({
      left: scrollExtent,
      // behavior: "smooth",
    });
  }

  sendWSOnClose (navigateTo?:any) {
    this.browserRefresh = !browserRefresh;
    let queryMessage: Message = {
      type: '',
      content: {},
    };
    queryMessage.type = "CloseEvent";
    queryMessage.content = {
      "F5Refresh": ''
    }

  }

  public open(modal: any): void {
    this.modalService.open(modal);
  }

  sendWSConnected() {
    let loginMessage: Message = {
      type: '',
      content: {},
    };

    var loginInfo: LoginInfo | undefined = this.userService.getLoginInfo();
    loginMessage.type = "webSocket_Connected";
    // loginMessage.content = "Client Connected";
    loginMessage.content = {
      "message": "Client Connected",
      "username": loginInfo?.user.name,
      "userlevel": loginInfo?.user.level,
      "loginID": loginInfo?.id,
    }

    this.WebSocketService.messages.next(loginMessage)

    this.getBuildingName()
  }

  getBuildingName() {
    let buildingMsg: Message =  {
      type: 'Get_BuildingName',
      content: '',
    }

    this.WebSocketService.messages.next(buildingMsg)
  }

  changeSiteLanguage(localeCode: string) : void {
    const selectedLanguage = this.languageList.find((language) => language.code === localeCode)?.label.toString()

    if (selectedLanguage) {
      this.siteLanguage = selectedLanguage
      this.translate.use(localeCode)
      console.log("localCode", localeCode)
      localStorage.setItem('LANG', localeCode)
    }

    setTimeout(() => {
      this.currentLanguage = this.translate.currentLang
      console.log('currentLanguage', this.currentLanguage)
    }, 300);   
  }

  getAlarmStatusTitle() {
    // switch(this.currentLanguage) {
    //   case "zh_HK":
    //     return this.alarmStatus?.systemName_CN
    //   case "zh_CN":
    //     return this.alarmStatus?.systemName_SC
    //   default:
    //     return this.alarmStatus?.systemName_EN
    // }
    return this.translate.instant("alarm_status")
  }

  getStartTimeTitle() {
    const element: HTMLElement = document.getElementById('startTimeHeader') as HTMLElement
    let startTimeTxt = this.translate.instant("start_time");
    let splitTxts = startTimeTxt.replace("(", "\n(").split("\n");
    if (splitTxts.length==2) {
      element.innerHTML = splitTxts[0] + "<br/>" + splitTxts[1];
    } else {
      element.innerHTML = splitTxts[0];
    }
  }

  getSystemName(system: SystemData) {
    switch(this.currentLanguage) {
      case "zh_HK":
        return system.systemName_CN
      case "zh_CN":
        return system.systemName_SC
      default:
        return system.systemName_EN
    }
  }

  getConStatus(device: DeviceData) {
    switch(this.currentLanguage) {
      case "zh_HK":
        return device.conStatus_CN
      case "zh_CN":
        return device.conStatus_SC
      default:
        return device.conStatus_EN
    }
  }

  getBackgroundColor(colIndex: number) {
    if (colIndex % 2 == 0) {
      return 'beige';
    } else {
      return 'beige';
    }
  }

  getDurationTime(device: DeviceData) {
    switch(this.currentLanguage) {
      case "zh_HK":
        return device.durationTime_CN
      case "zh_CN":
        return device.durationTime_SC
      default:
        return device.durationTime_EN
    }
  }

  getAlarmName(device: DeviceData) {
    switch(this.currentLanguage) {
      case "zh_HK":
        return device.nameCN
      case "zh_CN":
        return device.nameSC
      default:
        return device.nameEN
    }
  }

  getAlarmBody(device: DeviceData) {
    switch(this.currentLanguage) {
      case "zh_HK":
        return device.bodyCN
      case "zh_CN":
        return device.bodySC
      default:
        return device.bodyEN
    }
  }
  
  toggleMoreOrLess() {
    this.isExpanded = !this.isExpanded;

    if (this.isExpanded) {
      this.dataSource = this.alarmStatus?.deivceData;
    } else {
      this.dataSource = this.alarmStatus?.deivceData?.slice(0,3);
      this.scrollToTop();
    }
  }

  showMoreOrLess() {
    if (this.isExpanded) {
      return this.translate.instant('show_less');
    } else {
      return this.translate.instant("show_more");
    }
  }

  // showAll() {
  //   const devices = this.alarmStatus?.deivceData;
  //   if (this.isExpanded) {
  //     // const devices = this.alarmStatus?.deivceData;
  //     this.dataSource = new MatTableDataSource(devices);
  //     return devices;
  //   }
  //   this.dataSource = new MatTableDataSource(this.alarmStatus?.deivceData!.slice(0, 3));
  //   return this.alarmStatus?.deivceData!.slice(0, 3);
  // }

  sendData() {

    let _filterLogListsData = Constants.FILTER_LOG_LIST_DATA
    this.WebSocketService.messages.next(_filterLogListsData)
    let _plSystemData = Constants.PL_SYSTEM_DEVDATA
    let _mvSystemData = Constants.MV_SYSTEM_DEVDATA
    let _waterLeakageSystemData = Constants.WATER_LEAKAGE_SYSTEM_DEVDATA
    let _elSystemData = Constants.EL_SYSTEM_DEVDATA
    let _fuelSupplySystemData = Constants.FUEL_SUPPLY_SYSTEM_DEVDATA
    this.WebSocketService.messages.next(_plSystemData)
    this.WebSocketService.messages.next(_mvSystemData)
    this.WebSocketService.messages.next(_waterLeakageSystemData)
    this.WebSocketService.messages.next(_elSystemData)
    this.WebSocketService.messages.next(_fuelSupplySystemData)

    let _alarmData = Constants.SAMPLE_ALARM_SYSTEM_DATA
    this.WebSocketService.messages.next(_alarmData)

    let _serverStatus = {"type":"get_LocalServerStatus","content":{"localServerStatus":true}};
    this.WebSocketService.messages.next(_serverStatus)

    let _companyName = {
      type:"Get_BuildingName",
      content:{
            EN:"ABCBuilding",//英文
            CN:"ABC大廈",//繁體
            SC:"ABC大廈"//簡體
      }
    }
    this.WebSocketService.messages.next(_companyName)
  }

  logout() {
    this.userService.discardUserLogin();
    this.authService.logout(this.WebSocketService);

    this.closeWebsocket('/auth');
    // this.router.navigateByUrl('/auth');
  }

  showDeviceDialog(device: DeviceData, sysData: SystemData, sysKey: string) {
    this.selected = device;

    this.dialogRef = this.dialog.open(ShowDeviceDialogComponent, { panelClass: 'show-device', 
    data: new SystemDeviceData(device, sysData, sysKey), 
    width: '50%', 
    minWidth: '500px', 
    autoFocus: false, });

    this.dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
      if (result.save) {
        //Remind user
        this.dialog.open(RemindUserDialogComponent);
      }
    });
    // this.dialogRef.componentInstance.onRefreshData.subscribe(() => {
    //   this.sendDataOption2(); //data content a bit differ
    // });
  }

  filterDeviceLogs() {
    this.closeWebsocket([FilterDeviceLogComponent.route]);
    // this.router.navigate([FilterDeviceLogComponent.route]);
  }

  viewDownloadReport() {
    this.closeWebsocket([ViewDownloadReportComponent.route]);
    // this.router.navigate([ViewDownloadReportComponent.route]);
  }

  home() {
    this.closeWebsocket([AdminComponent.route]);
    // this.router.navigate([AdminComponent.route]);
  }

  // _isAdminUser() {
    //check if userLevel is 2
    // return this.userService.getLoginInfo()?.user.level ? this.userService.getLoginInfo()?.user.level == 2 ? true : false : false;
  // }

  _getUserInfo() {
    let stored = sessionStorage.getItem('loginInfo');
    if (stored != undefined) {
      let loginInfo : LoginInfo = JSON.parse(stored!);
      this.userService.saveLoginInfo(loginInfo);

      this.username = loginInfo.user.name
      this.isAdmin = loginInfo.user.level == 2 
    }
    // return this.userService.getUserInfo() ? this.userService.getUserInfo()!.name : "User-1";
  }

  closeWebsocket(navigateTo?: any) {
    // if (!this.WebSocketService.isClosed()) {
      this.sendWSOnClose() //TODO: is this required anymore?
      console.log("******** close websocket *********")
      this.WebSocketService.close()
    // }

    if (navigateTo) {
      if (typeof(navigateTo) == 'string') {
        this.router.navigateByUrl(navigateTo)
      } else {
        this.router.navigate(navigateTo)
      }
    }
  }
}

function isJsonString(str: string) {
  try {
    JSON.parse(str);
  } catch (e) {
    return false;
  }
  return true;
}