import { Component, EventEmitter, Input, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { MatSidenavModule } from '@angular/material/sidenav';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent {
  siteLanguage = 'English';
  languageList = [
    {code: 'en', label: 'English'},
    {code: 'zh_HK', label: '繁體中文'},
    {code: 'zh_CN', label: '简体中文'}
  ];
  currentLanguage: string;

  @Input()
  buildingNames: string; //JSON string

  @Input()
  pageTitle: string //translated title

  @Input()
  localServerStatus: string;

  @Input()
  dateTime: string;

  @Input()
  username: string;

  @Input()
  minWidth: string;

  @Output()
  backBtnClicked = new EventEmitter<void>();

  @Output()
  langChanged = new EventEmitter<string>();

  constructor(public translate: TranslateService) {
    let savedLang = localStorage.getItem("LANG");
    if (savedLang != null) {
      this.siteLanguage = savedLang;
      translate.use(savedLang);
      this.currentLanguage = translate.currentLang;
      console.log("currentLanguage", this.currentLanguage);
    }
  }

  getCompanyTitle() {
    if (this.buildingNames != undefined) {
      let buildingTitles = JSON.parse(this.buildingNames);
      switch(this.currentLanguage) {
        case "zh_HK":
          return buildingTitles.CN
        case "zh_CN":
          return buildingTitles.SC
        default:
          return buildingTitles.EN
      }
    } else {
      return this.translate.instant("app_title");
    }
  }

  getOnlineStatusColor() {
    if (this.localServerStatus != undefined) {
      if (this.localServerStatus) {
        return 'lightgreen';
      } else {
        return 'red';
      }
    } else {
      return "white";
    }
  }

  getOnlineStatus() {
    if (this.localServerStatus != undefined) {
      if (this.localServerStatus) {
        return this.translate.instant("online");
      } else {
        return this.translate.instant("offline");
      }
    } else {
      return "-";
    }
  }

  //Not equals to changeSiteLanguage in Parent components
  changeLanguage(localeCode: string) : void {
    const selectedLanguage = this.languageList.find((language) => language.code === localeCode)?.label.toString()

    if (selectedLanguage) {
      this.langChanged.emit(localeCode); //tell the parent to change other components as well
    }

    setTimeout(() => {
       this.currentLanguage = this.translate.currentLang
    }, 300);
  }
}