import { AfterContentInit, Component, ElementRef, HostBinding, Input, OnChanges, OnInit, SimpleChanges, ViewChild } from "@angular/core";

@Component(
    {
        selector: 'app-flashing-dot',
        templateUrl: './flashing-dot.component.html',
        styleUrls: ['./flashing-dot.component.css']
    }
)
export class FlashingDotComponent implements OnInit, AfterContentInit, OnChanges {
    private svgStyle: CSSStyleDeclaration;

    @ViewChild('svgImage', { static: true })
    private svgImageRef: ElementRef<HTMLDivElement>

    @Input()
    name: string;

    @Input('start')
    // @HostBinding("style.--first-color")
    firstColorHexCode: string = "#ff0000";

    @Input('end')
    // @HostBinding("style.--second-color")
    secondColorHexCode: string = "#00ff00";

    @Input('endSecond')
    extraSecondColor: string = "transparent";

    // @Input()
    // startColor: string;
    // @Input()
    // endColor: string;

    constructor() {
        console.log("[FD] constructor will call first");
    }
    ngOnInit(): void {
        //extract your input(s) beyond ngOnInit
        console.log("[FD] My name is : " + this.name);
        console.log("[FD] start color : ", this.firstColorHexCode);
        console.log("[FD] end color : ", this.secondColorHexCode);
    }
    ngAfterContentInit(): void {
        //change DOM properties (CSS variable) here
        console.log("[FD] ngAfterContentInit");
        this.svgStyle = this.svgImageRef.nativeElement.style;
        this.svgStyle.setProperty(
          '--first-color', this.firstColorHexCode);
        this.svgStyle.setProperty(
          '--second-color', this.secondColorHexCode);
        this.svgStyle.setProperty(
          '--extra-second-color', this.extraSecondColor);
    }
    ngOnChanges(changes: SimpleChanges): void {
        if (changes['firstColorHexCode'] 
          && !changes['firstColorHexCode'].isFirstChange) {
          this.svgStyle.setProperty(
            '--first-color', changes['firstColorHexCode'].currentValue);
        }
        if (changes['secondColorHexCode'] 
          && !changes['secondColorHexCode'].isFirstChange) {
          this.svgStyle.setProperty(
            '--second-color', changes['secondColorHexCode'].currentValue);
        }
        if (changes['extraSecondColor'] 
          && !changes['extraSecondColor'].isFirstChange) {
          this.svgStyle.setProperty(
            '--extra-second-color', changes['extraSecondColor'].currentValue);
        }
      }
}