import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-add-group-fail-dialog',
  templateUrl: './add-group-fail-dialog.component.html',
  styleUrls: ['./add-group-fail-dialog.component.css'],
})
export class AddGroupFailDialogComponent {
  constructor(
    private dialogRef: MatDialogRef<AddGroupFailDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data) {}

  addNew() {
    this.dialogRef.close({ reset: true }); //close dialog
  }

  navigateTo(path: string) {
    this.dialogRef.close({'path':path}); //close dialog
  }
}
