import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-system-submit-dialog',
  templateUrl: './add-system-submit-dialog.component.html',
  styleUrls: ['./add-system-submit-dialog.component.css']
})
export class AddSystemSubmitDialogComponent {
  constructor(private router: Router, private dialogRef: MatDialogRef<AddSystemSubmitDialogComponent>) {}

  addNew() {
    this.dialogRef.close({'reset': true}); //close dialog
  }

  navigateTo (path: string) {
    this.dialogRef.close({'path': path}); //close dialog
  }
}