import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { SharedService } from 'src/app/service/shared.service';

@Component({
  selector: 'app-confirm-delete-system-dialog',
  templateUrl: './confirm-delete-system-dialog.component.html',
  styleUrls: ['./confirm-delete-system-dialog.component.css']
})
export class ConfirmDeleteSystemDialogComponent {
  isEditSystem : boolean = false;

  constructor(private router: Router, private dialogRef: MatDialogRef<ConfirmDeleteSystemDialogComponent>, private sharedSerice: SharedService, @Inject(MAT_DIALOG_DATA) public data: any) {}

  ngOnInit() {
    this.isEditSystem = this.sharedSerice.isEditSystem;
  }

  delete(clear: boolean) {
    this.dialogRef.close({'confirm': clear}); //close dialog
  }

  navigateTo (path: string) {
    this.dialogRef.close(); //close dialog
    this.router.navigateByUrl(path);
  }
}
