import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-login-error-dialog',
  templateUrl: './login-error-dialog.component.html',
  styleUrls: ['./login-error-dialog.component.css']
})
export class LoginErrorDialog {
  browserRefresh: boolean;
  constructor(
    private dialogRef: MatDialogRef<LoginErrorDialog>,
    @Inject(MAT_DIALOG_DATA) public data) {}   

  close(isConfirm: boolean): void {
    this.dialogRef.close(isConfirm)
  }
}
