import { LogType, SystemName } from "./preview/filter-device-log/filter-device-log.component";

export class DeviceLog {
    public processId?: number;
    public over1000?: boolean;
    public content?: DeviceLogContent[];

    static isValid(obj: any) {
        if (obj.content != null) {
            return true
        } return false
    }
}

export class DeviceLogContent {
     id?: string;
     type?: LogType; //en,sc,cn
     system?: SystemName; //en, sc, cn
     floor?: string;
     name?: Language; //en,sc,cn
     designation?: string;
     description?: Language;
     durationMins?: string;
     durationStatus?: string;
     startTime?: number; //epochtime
     time?: string;
     body?: Language; //en,sc,cn
     lampKey?: string; //original "lamp" property
     lamp?: Language; //lamp_EN, lamp_SC, lamp_CN
     change?: object;
}

export class Change {
    public before?: string;
    public after?: string;

    constructor(mapOfData:any) {
        // console.log(`mapOfData : ${mapOfData}`);
        this.before = mapOfData["Before"];
        this.after = mapOfData["After"];
    }
}

export class Language {
    langEN?: string;
    langSC?: string;
    langTC?: string;

    constructor(en:string, sc:string, tc:string) {
        this.langEN = en;
        this.langSC = sc;
        this.langTC = tc;
    }
}