import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { ConfirmDialogComponent } from '../preview/confirm-dialog/confirm-dialog.component';

@Component({
  selector: 'app-remind-login-dialog',
  templateUrl: './remind-login-dialog.component.html',
  styleUrls: ['./remind-login-dialog.component.css']
})
export class RemindLoginDialogComponent {
  browserRefresh: boolean;
  constructor(
    private remindLoginRef: MatDialogRef<ConfirmDialogComponent>) {}

  close(isConfirm: boolean) {
    this.remindLoginRef.close(isConfirm)
  }
}
