import { Component, Inject, Input } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-confirm-dialog',
  templateUrl: './confirm-dialog.component.html',
  styleUrls: ['./confirm-dialog.component.css']
})
export class ConfirmDialogComponent {

  constructor(@Inject(MAT_DIALOG_DATA) public data: any, private dialogRef: MatDialogRef<ConfirmDialogComponent>, private translate: TranslateService){
    console.log('data : ' + data.toString());
  };

  close(accept: boolean) {
    this.dialogRef.close({'confirm' : accept});
  }

  getMuteText(isMute: boolean) {
    if (isMute) {
      return this.translate.instant("true_flag");
    }
    return this.translate.instant("false_flag");
  }

  getDisableText(isDisable: boolean) {
    if (isDisable) {
      return this.translate.instant("true_flag");
    }
    return this.translate.instant("false_flag");
  }
}
