import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Equipment } from 'src/app/equipment-setting';
import { SystemDetail } from 'src/app/system-detail';

@Component({
  selector: 'app-equipment-detail',
  templateUrl: './equipment-detail.component.html',
  styleUrls: ['./equipment-detail.component.css']
})
export class EquipmentDetailComponent {
  @Input()
  system: SystemDetail

  @Input()
  currentLanguage

  @Output()
  // showDevice = new EventEmitter<{dev: Equipment, sys: SystemDetail, key: string}>();

  getDeviceColumnHeight() {
    switch(this.currentLanguage) {
      case "zh_HK":
      case "zh_CN":
        return '125px'
      default:
        return '150px'
    }
  }

 getItem(key:string, value: string) {
  return value[key];
 }

  getEquipmentName(equipment: Equipment) {
    switch(this.currentLanguage) {
      case "zh_HK":
        return equipment.name_CN;
      case "zh_CN":
        return equipment.name_SC;
      case 'en_US':
      default:
        return equipment.name_EN;
    }
  }
}
