export class DeviceData {
    public floor?: string;
    public nameEN?: string;
    public nameCN?: string;
    public nameSC?: string;
    public designation?: string;
    public status?: Object;
    public startTime?: string;
    public durationTime_EN?: string;
    public durationTime_CN?: string;
    public durationTime_SC?: string;
    public time?: string;
    public conStatus?: string;
    public conStatus_EN?: string;
    public conStatus_CN?: string;
    public conStatus_SC?: string;
    public bodyEN?: string;
    public bodySC?: string;
    public bodyCN?: string;
    public statusLED?: string;
    public statusEN?: string;
    public statusCN?: string;
    public statusSC?: string;
    public unmute?: boolean;
    public enable?: boolean;
    public colorCodes?: string[];
    
    colorCodesLength() {
        if (this.colorCodes != undefined && this.colorCodes != null) {
            return this.colorCodes.length;
        }
        return -1;
    }

    primaryStatusColor() {
        if (this.colorCodesLength() > 0) {
            return this.colorCodes[0];
        }
        else {
            return "Grey";
        }
    }

    secondaryStatusColor() {
        if (this.colorCodesLength() > 1) {
            return this.colorCodes[1];
        }
        else {
            return this.primaryStatusColor();
        }
    }

    constructor(mapOfData: any) {
        this.floor = mapOfData['Floor']
        this.nameEN = mapOfData['Name_EN']
        this.nameCN = mapOfData['Name_CN']
        this.nameSC = mapOfData['Name_SC']
        this.designation = mapOfData['Designation']
        this.status = mapOfData['Status']
        this.conStatus = mapOfData['Con_Status']
        this.conStatus_EN = mapOfData['Con_Status_EN']
        this.conStatus_CN = mapOfData['Con_Status_CN']
        this.conStatus_SC = mapOfData['Con_Status_SC']
        this.statusLED = mapOfData['Status_LED']
        this.statusEN = mapOfData['Status_EN']
        this.statusCN = mapOfData['Status_CN']
        this.statusSC = mapOfData['Status_SC']
        this.startTime = mapOfData['Start_Time']
        this.durationTime_EN = mapOfData['Duration_Time_EN']
        this.durationTime_CN = mapOfData['Duration_Time_CN']
        this.durationTime_SC = mapOfData['Duration_Time_SC']
        this.bodyEN = mapOfData['Body_EN']
        this.bodyCN = mapOfData['Body_CN']
        this.bodySC = mapOfData['Body_SC']
        this.time = mapOfData['Time'];
        this.unmute = mapOfData['Unmute']
        this.enable = mapOfData['Enable']
        this.colorCodes = mapOfData['Color_Code']
    }
}

