import { Component, Inject } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { environment } from 'src/environments/environment';
import { Constants } from '../constants';
import { AuthService } from '../service/auth.service';
import { UserService } from '../service/user.service';
import { WebsocketService } from '../service/websocket.service';
import { User } from '../user';
import { Message } from '../message';
import { AppComponent } from '../app.component';
import { TranslateService } from '@ngx-translate/core';
import { ConnectionDownDialog } from '../connection-down-dialog/connection-down-dialog.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { LoginErrorDialog } from '../login-error-dialog/login-error-dialog.component';
import { PreviewComponent } from '../preview/preview.component';

function isJsonString(str: string) {
  try {
    JSON.parse(str);
  } catch (e) {
    return false;
  }
  return true;
}

@Component({
  selector: 'app-auth',
  templateUrl: './auth.component.html',
  styleUrls: ['./auth.component.css'],
  providers: [WebsocketService]
})
export class AuthComponent {
  authForm!: FormGroup;
  isSubmitted = false;
  //environment configuration (refer to /environment)
  isDev: boolean = false;

  siteLanguage = 'English';
  languageList = [
    {code: 'en', label: 'English'},
    {code: 'zh_HK', label: '繁體中文'},
    {code: 'zh_CN', label: '简体中文'}
  ];
  currentLanguage?: String;
  public timer;
  private dialogRef?: MatDialogRef<ConnectionDownDialog>;
  private eDialogRef?: MatDialogRef<LoginErrorDialog>;

  constructor(@Inject(WebsocketService) private WebSocketService: WebsocketService, private dialog: MatDialog, private userService: UserService, private route: ActivatedRoute, private authService: AuthService, private router: Router, private formBuilder: FormBuilder, private translate: TranslateService) {
    let savedLang = localStorage.getItem("LANG");
    if (savedLang != null) {
      this.siteLanguage = savedLang;
      translate.use(savedLang);
      this.currentLanguage = translate.currentLang;
      console.log("currentLanguage",this.currentLanguage);
    }
    // console.log('production? : ' + environment.production);
    this.isDev = !environment.production;

    WebSocketService.messages.subscribe(msg => {
      console.log("[auth] Response from websocket: " + msg);

      if (this.timer) {
        clearTimeout(this.timer)
      }

      if (AppComponent.isCallingOnClose) {
        //reset
        // setTimeout(() => {this.isCallingOnClose = false;},200);
        AppComponent.isCallingOnClose = false;
      }

      this.authForm.controls['email'].setErrors({ 'user_not_exist': false });
      this.authForm.controls['password'].setErrors({ 'incorrect_password': false });
      this.authForm.controls['password'].setErrors({ 'timeout': false });
      this.authForm.controls['password'].setErrors({ 'system_error': false });

      if (isJsonString(msg.toString())) {
        var jobject = JSON.parse(msg.toString());
        if (jobject.type === "get_userAuthResult") {
          let result = jobject.content["result"];
          let errorCode = jobject.content["error_Code"];

          if (result == "Success") {
            let username = jobject.content["username"]

            this.userService.saveUserLogin({
              mUsername: username,
              mUserLevel: null,
              processId: jobject.content["processId"],
              authResult: result,
              loginID: jobject.content["loginID"]
            })

            localStorage.setItem('USERNAME', username);

            this.closeWebsocket('/admin');
          } else if (errorCode == this.USER_LOGIN_ALREADY) {
            this.showLoginErrorDialog("user_login_already");
            return this.authForm.controls["email"].setErrors({ 'user_login_already': true });
          } else if (errorCode == this.USER_NOT_EXIST) {
            this.showLoginErrorDialog("user_not_exist");
            return this.authForm.controls["email"].setErrors({ 'user_not_exist': true });
          } else if (errorCode == this.INCORRECT_PASSWORD) {
            this.showLoginErrorDialog("incorrect_password");
            return this.authForm.controls["email"].setErrors({ 'incorrect_password': true });
          } else if (errorCode == this.TIMEOUT) {
            this.showLoginErrorDialog("timeout");
            return this.authForm.controls["email"].setErrors({ 'timeout': true });
          } else if (errorCode == this.SYSTEM_ERROR) {
            this.showLoginErrorDialog("system_error");
            return this.authForm.controls["email"].setErrors({ 'system_error': true });
          } else {
            this.showLoginErrorDialog("unknown_error");
          }

          // Used in Portal
          // if (jobject.content["userauthresult"] == "auth_success") {
          //   this.userService.saveUserLogin({
          //     mUsername: jobject.content["username"],
          //     mUserLevel: jobject.content["userlevel"],
          //     processId: jobject.content["processid"],
          //     authResult: jobject.content["userauthresult"],
          //     loginID: jobject.content["loginID"]
          //   })

          //   localStorage.setItem('USERNAME', jobject.content["username"]);

          //   this.closeWebsocket('/preview');
          // }
          // else if (jobject.content['userauthresult'] == "user_nonexistent") {
          //   this.showLoginErrorDialog("user_not_exist");
          //   return this.authForm.controls['email'].setErrors({ 'user_not_exist': true });
          // } else if (jobject.content['userauthresult'] == "password_incorrect") {
          //   this.showLoginErrorDialog("incorrect_password");
          //   return this.authForm.controls['password'].setErrors({ 'incorrect_password': true });
          // }
          // this.router.navigateByUrl('/admin');
        }
        else {
          console.warn("sending login request (test data)");
          this.authForm.controls['password'].setErrors({ 'incorrect_password': true });
          
          if(this.isDev) {
            if(jobject.type === "user_login") {
              let userLoginPrompt;
              if (jobject.content['username']=='usr') {
                userLoginPrompt = Constants.NORMAL_USER_LOGIN
              } else {
                userLoginPrompt = Constants.ADMIN_USER_LOGIN
              }
              this.WebSocketService.messages.next(userLoginPrompt);
            }
          }
         
          return;
        }
      }
      // console.warn("call unsubscribe on Auth WebSocket Service");
      // WebSocketService.messages.unsubscribe();
      // return this.router.navigateByUrl('/admin');
      // return this.router.navigateByUrl(PreviewComponent.route);
    });
  }

  ngOnInit() {
    let savedUsername = localStorage.getItem('USERNAME') ?? "";

    this.authForm = this.formBuilder.group({
      email: [savedUsername, Validators.required],
      password: ['', Validators.required]
    });

    window.addEventListener('beforeunload', () => {
      //call when user clicked refresh
      if (!AppComponent.isCallingOnClose) {
        AppComponent.isCallingOnClose = true;

        console.log("Message onClose (auth)"); 
        //TODO: can't differentiate between refresh and close window
        //this is auth
        this.sendWSOnClose();
      }
    }, false);
    
    window.addEventListener('popstate', () => {
      //back button clicked
      if (!AppComponent.isCallingOnClose) {
        AppComponent.isCallingOnClose = true;
        this.closeWebsocket();
      }
    }, false);
  }

  getCurrentLanguage() {
    switch(this.currentLanguage) {
      case "zh_HK":
        return "繁體中文"
      case "zh_CN":
        return "简体中文"
      default:
        return "English"
    }
  }
  
  changeSiteLanguage(localeCode: string) : void {
    const selectedLanguage = this.languageList.find((language) => language.code === localeCode)?.label.toString()

    if (selectedLanguage) {
      this.siteLanguage = selectedLanguage
      this.translate.use(localeCode)
      console.log("localCode", localeCode)
      localStorage.setItem('LANG', localeCode)
    }
    setTimeout(() => {
      this.currentLanguage = this.translate.currentLang
      console.log('currentLanguage', this.currentLanguage)
    }, 300)  
  }

  closeWebsocket(navigateTo?: any) {
    if (!this.WebSocketService.isClosed()) {
      this.sendWSOnClose() //TODO: is this required anymore?
      console.log("******** close websocket *********")
      this.WebSocketService.close()
    }

    if (navigateTo) {
      if (typeof(navigateTo) == 'string') {
        this.router.navigateByUrl(navigateTo)
      } else {
        this.router.navigate(navigateTo)
      }
    }
  }

  sendWSOnClose (navigateTo?: string) {
    // this.browserRefresh = !browserRefresh;
    let queryMessage: Message = {
      type: '',
      content: {},
    };
    queryMessage.type = "CloseEvent";
    queryMessage.content = {
      "F5Refresh": ''
    }

    this.WebSocketService.messages.next(queryMessage)

    if (navigateTo) {
      this.router.navigateByUrl(navigateTo)
    }
  }

  get formControls() { return this.authForm.controls; }

  signIn(mUsername: string, mPassword: string) {
    this.authForm.controls['email'].setValue(mUsername);
    this.authForm.controls['password'].setValue(mPassword);
    this.isSubmitted = true;
    if (this.authForm.invalid) {
      return;
    }
    let userData: User = {
      username: mUsername,
      password: mPassword,
    };

    // if (!this.isDev) {
      this.updateLoginTimer()
    // }

    // -- Test login errors --
    // this.showLoginErrorDialog("incorrect_password"); //Show dialog
    // this.authForm.controls['password'].setErrors({ 'incorrect_password': true }); //Show error text
    // this.showLoginErrorDialog("user_not_exist");
    // this.authForm.controls['email'].setErrors({ 'user_not_exist': true });
    
    this.authService.signIn(this.WebSocketService, userData);
  }

  updateLoginTimer() {
    if (this.timer !== null) {
      clearTimeout(this.timer)
      // console.warn("clear 10 seconds timeout")
    }
    // console.error("set 10 seconds timeout")
    this.timer = setTimeout(() => {
      console.log("10 seconds over")
      this.showConnectionDownDialog()
    }, 10000);
  }

  showConnectionDownDialog() {
    this.dialogRef = this.dialog.open(ConnectionDownDialog, { panelClass: 'show-device'});

    this.dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

  showLoginErrorDialog(errorMessage:string) {
    this.eDialogRef = this.dialog.open(LoginErrorDialog, {minWidth:'400px', height: '200px', data : errorMessage})

    this.eDialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

  //For testing purpose
  adminUserResponse() {
    //userlevel = 2;
    let userData: User = {
      username: 'adm',
      password: '123',
    };
    this.authService.signIn(this.WebSocketService, userData);

    let adminLoginPrompt = Constants.ADMIN_USER_LOGIN
    this.WebSocketService.messages.next(adminLoginPrompt);
  }

  //For testing purpose
  normalUserResponse() {
    //userlevel = 1;
    let userData: User = {
      username: 'usr',
      password: '123',
    };
    this.authService.signIn(this.WebSocketService, userData);

    let userLoginPrompt = Constants.NORMAL_USER_LOGIN
    this.WebSocketService.messages.next(userLoginPrompt);
  }

  //For testing purpose
  getAuthResponse() {
    let queryMessage: Message = {
      type: '',
      content: {},
    };
    queryMessage.type = "get_userAuthResult";
    queryMessage.content = {
      "processid" : "1000",
      "username" : "usr",
      "result" : "Success",
      "loginID": "1"
    };

    localStorage.setItem('ACCESS_TOKEN', "access_token");
    this.WebSocketService.messages.next(queryMessage)
  }

  //error codes
  USER_LOGIN_ALREADY = 1012;
  USER_NOT_EXIST = 1010;
  INCORRECT_PASSWORD = 1011;
  TIMEOUT = 1000;
  SYSTEM_ERROR = 1001;

  //For testing purpose
  testLoginFail(errorCode: number) {
    let result;

    switch(errorCode) {
      case this.USER_LOGIN_ALREADY:
        result = "User login already";
        // errorCode = 1012;
        break;
      case this.USER_NOT_EXIST:
        result = "User not exist";
        // errorCode = 1010;
        break;
      case this.INCORRECT_PASSWORD:
        result = "Incorrect Password";
        // errorCode = 1011;
        break;
      case this.TIMEOUT:
        result = "Timeout, please try again. (Timeout 5 Seconds)";
        // errorCode = 1000;
        break;
      case this.SYSTEM_ERROR:
        result = "System Error!";
        // errorCode = 1001;
    }

    let queryMessage: Message = {
      type: '',
      content: {},
    };
    queryMessage.type = "get_userAuthResult";
    queryMessage.content = {
      "processid" : "1000",
      "username" : "usr",
      "result" : result,
      "error_Code": errorCode,
      "loginID": "1"
    };

    this.WebSocketService.messages.next(queryMessage)
  }
}
