import { Component, EventEmitter, Input, Output } from '@angular/core';
import { DeviceData } from 'src/app/device-data';
import { SystemData } from 'src/app/system-data';

@Component({
  selector: 'app-device-detail',
  templateUrl: './device-detail.component.html',
  styleUrls: ['./device-detail.component.css']
})
export class TableStatusComponent {
  @Input()
  system

  @Input()
  currentLanguage

  @Output()
  showDevice = new EventEmitter<{dev: DeviceData, sys: SystemData, key: string}>();

  getDeviceColumnHeight() {
    switch(this.currentLanguage) {
      case "zh_HK":
      case "zh_CN":
        return '125px'
      default:
        return '150px'
    }
  }

  getDeviceName(device: DeviceData) {
    switch(this.currentLanguage) {
      case "zh_HK":
        return device.nameCN
      case "zh_CN":
        return device.nameSC
      default:
        return device.nameEN
    }
  }
}
