import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Constants } from 'src/app/constants';
import { DownloadFile } from 'src/app/file';
import { Message } from 'src/app/message';
import { AuthService } from 'src/app/service/auth.service';
import { LoginInfo, UserService } from 'src/app/service/user.service';
import { WebsocketService } from 'src/app/service/websocket.service';
import { environment } from 'src/environments/environment';
import { FilterDeviceLogComponent } from '../filter-device-log/filter-device-log.component';
import { HttpClient } from '@angular/common/http';
import { AppComponent, browserRefresh } from 'src/app/app.component';
import { MatTableDataSource } from '@angular/material/table';
import { TranslateService } from '@ngx-translate/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { RemindLoginDialogComponent } from 'src/app/remind-login-dialog/remind-login-dialog.component';

@Component({
  selector: 'app-view-download-report',
  templateUrl: './view-download-report.component.html',
  styleUrls: ['./view-download-report.component.css']
})
export class ViewDownloadReportComponent implements OnInit, OnDestroy {
  static route = "admin/reports";

  displayColumns: string[] = ['uploadYear','name','fileSize','lastModifyTime'];
  dataSource:any;

  //environment configuration (refer to /environment)
  isDev: boolean = false;

  browserRefresh = false;

  downloadFiles?: DownloadFile[] = [];
  showMenu = false;

  username?: String;
  dateTime?: string;

  fileResponseReturned = false;
  windowScrolled = false;

  siteLanguage = 'English';
  languageList = [
    {code: 'en', label: 'English'},
    {code: 'zh_HK', label: '繁體中文'},
    {code: 'zh_CN', label: '简体中文'}
  ];
  currentLanguage?: String;

  private timer;
  private remindLoginRef?: MatDialogRef<RemindLoginDialogComponent> //add import
  localServerStatus: boolean = undefined;

  constructor(@Inject(WebsocketService) private WebSocketService: WebsocketService,
    private userService: UserService,
    private authService: AuthService,
    private router: Router,
    private http: HttpClient,
    private translate: TranslateService,
    private dialog: MatDialog) {

      let savedLang = localStorage.getItem("LANG");
    if (savedLang != null) {
      this.siteLanguage = savedLang;
      translate.use(savedLang);
      this.currentLanguage = translate.currentLang;
      console.log("currentLanguage",this.currentLanguage);
    }
    console.log('production? : ' + environment.production);
    this.isDev = !environment.production;
    
    WebSocketService.reconnect()
    console.log("++++++++ reconnect websocket ++++++++")
    this.WebSocketService.messages.subscribe(message => {
      console.log("[report] Response from websocket: " + message);

      this.updateSessionTimer();

      AppComponent.isCallingOnClose = false;

      if (isJsonString(message.toString())) {
        let timeNow = new Date();
        const result = timeNow.toLocaleTimeString("en-GB", { // you can use undefined as first argument
          hour: "2-digit",
          minute: "2-digit",
          second: "2-digit",
        })
        this.dateTime = result;

        var jObject = JSON.parse(message.toString());
        
        if (jObject.type.endsWith("LocalServerStatus")) {
          this.localServerStatus = jObject.content["localServerStatus"];
        } else if (jObject.type === 'get_PDFFiles') {
          this.fileResponseReturned = true;
          this.downloadFiles = [];
          for (let fileObj of jObject.content['fileNameArray']) {
            let file = Object.assign(new DownloadFile(), fileObj);

            console.log(file.name);
            console.log(file.fileType);

            this.downloadFiles.push(file);
          }

          this.dataSource = new MatTableDataSource(this.downloadFiles);
        } else {
          if (this.downloadFiles == undefined || this.downloadFiles?.length == 0 && !this.fileResponseReturned) {
            //if download files not listed
            this.fileResponseReturned = true;
            this.getDownloadFiles();

            setTimeout(() => {
              this.fileResponseReturned = false;
            }, 2000);
          }
        }
      }
    });

    // this.getDownloadFiles();
  }
  ngOnInit(): void {
    this.browserRefresh = browserRefresh;
    console.log('refreshed?:', browserRefresh);
    // this.fileResponseReturned = false;

    this._getUserInfo();

    if (this.showMenu == false) {
      setTimeout(() => this.showMenu = true, 2000);
    }

    setTimeout (() => {
      if (browserRefresh) {
        let stored = sessionStorage.getItem('loginInfo');
        if (stored !== undefined) {
          console.log("execute webSocket_Connected after refresh [admin]")
          let loginInfo : LoginInfo = JSON.parse(stored!);
          this.userService.saveLoginInfo(loginInfo);
          this.sendWSConnected();
        }
        // this.sendWSOnClose(browserRefresh);
      } else {
        if (this.userService.getLoginInfo() != undefined) {
          console.log("execute webSocket_Connected after 1 sec [admin]")
          this.sendWSConnected()
        }
        //wait for a second
      }
    }, 1000)

    window.addEventListener('beforeunload', async () => {
      //call when user clicked refresh
      if (!AppComponent.isCallingOnClose) {
        console.log("Message onClose (reports)"); 
        this.sendWSOnClose();
      }
    } , false);

    window.addEventListener('popstate', () => {
      //back button clicked
      if (!AppComponent.isCallingOnClose) {
        AppComponent.isCallingOnClose = true;
        this.closeWebsocket();
      }
    }, false);
  }

  updateSessionTimer() {
    if (this.timer !== null) {
      clearTimeout(this.timer)
      // console.warn("clear 10 seconds timeout")
    }
    // console.error("set 10 seconds timeout")
    this.timer = setTimeout(() => {
      console.log("60 seconds over")
      this.showSessionTimeoutDialog()
    }, 60000);
  }

  showSessionTimeoutDialog() {
    this.remindLoginRef = this.dialog.open(RemindLoginDialogComponent, { panelClass: 'show-device'});

    this.remindLoginRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
      if (!result || result == undefined) {
        this.updateSessionTimer()
      } else {
        this.logout()
      }
    });
  }

  ngOnDestroy(): void {
    if (this.timer) {
      clearTimeout(this.timer)
    }
  }

  changeSiteLanguage(localeCode: string) : void {
    const selectedLanguage = this.languageList.find((language) => language.code === localeCode)?.label.toString()

    if (selectedLanguage) {
      this.siteLanguage = selectedLanguage
      this.translate.use(localeCode)
      console.log("localCode", localeCode)
      localStorage.setItem('LANG', localeCode)
    }

    this.currentLanguage = this.translate.currentLang
    console.log('currentLanguage', this.currentLanguage)
  }

  closeWebsocket(navigateTo?: any) {
    if (!this.WebSocketService.isClosed()) {
      this.sendWSOnClose() //TODO: is this required anymore?
      console.log("******** close websocket *********")
      this.WebSocketService.close()
    }

    if (navigateTo) {
      if (typeof(navigateTo) == 'string') {
        this.router.navigateByUrl(navigateTo)
      } else {
        this.router.navigate(navigateTo)
      }
    }
  }

  _getUserInfo() {
    let stored = sessionStorage.getItem('loginInfo');
    if (stored != undefined) {
      let loginInfo : LoginInfo = JSON.parse(stored!);
      this.userService.saveLoginInfo(loginInfo);

      this.username = loginInfo.user.name
      // this.isAdmin = loginInfo.user.level == 2 
    }
    // return this.userService.getUserInfo() ? this.userService.getUserInfo()!.name : "User-1";
  }

  sendWSOnClose (navigateTo?:any) {
    AppComponent.isCallingOnClose = true;
    this.browserRefresh = !browserRefresh;

    let queryMessage: Message = {
      type: '',
      content: {},
    };
    queryMessage.type = "CloseEvent";
    queryMessage.content = {
      "F5Refresh": ''
    }

    this.WebSocketService.messages.next(queryMessage)

    if (navigateTo) {
      if (typeof(navigateTo) == 'string') {
        this.router.navigateByUrl(navigateTo)
      } else {
        this.router.navigate(navigateTo)
      }
    }
  }

  sendWSConnected() {
    let loginMessage: Message = {
      type: '',
      content: {},
    };

    var loginInfo: LoginInfo | undefined = this.userService.getLoginInfo();
    loginMessage.type = "webSocket_Connected";
    // loginMessage.content = "Client Connected";
    loginMessage.content = {
      "message": "Client Connected",
      "username": loginInfo?.user.name,
      "userlevel": loginInfo?.user.level,
      "loginID": loginInfo?.id,
    }

    this.WebSocketService.messages.next(loginMessage)
  }

  logout() {
    this.userService.discardUserLogin();
    this.authService.logout(this.WebSocketService);
    this.closeWebsocket("/auth");
    // this.router.navigateByUrl('/auth');
  }

  filterDeviceLogs() {
    this.closeWebsocket([FilterDeviceLogComponent.route]);
    // this.router.navigate([FilterDeviceLogComponent.route]);
  }

  getDownloadFiles() {
    let sendDownloadFileRequest: Message = {
      type: 'getDownloadFiles',
      content: {},
    };

    this.WebSocketService.messages.next(sendDownloadFileRequest)
  }

  //For testing purposes
  getSamplePDFFiles() {
    let response = Constants.SAMPLE_PDF_FILES;
    this.WebSocketService.messages.next(response);
  }

  downloadFile(file: DownloadFile) {

      console.log('downloading \"'  + file.name + '\"');

      const downloadURL = file.Url;
      // const token = 'my JWT';
      // const headers = new HttpHeaders().set('authorization','Bearer '+token);

      if (file.Url === null || file.Url === undefined) return;

      this.http.get(downloadURL! ,{responseType: 'blob' as 'json'}).subscribe(
          (response: any) =>{
              let dataType = response.type;
              let binaryData = [];
              binaryData.push(response);
              let downloadLink = document.createElement('a');
              downloadLink.href = window.URL.createObjectURL(new Blob(binaryData, {type: dataType}));
              if (file.name!)
                  downloadLink.setAttribute('download', file.name!);
              document.body.appendChild(downloadLink);
              downloadLink.click();
          }
      )
  
  }

  getFileSizeDisplay(byte: number) {
    var finalResult;
    if(byte >= 1000 * 1000) {
      var final = (byte / (1000*1000));
      finalResult = parseFloat(final.toFixed(2)) + "MB";
    } else if (byte >= 1000) {
      var final = (byte / 1000);
      finalResult = parseFloat(final.toFixed(2)) + " KB";
    } else {
      finalResult = parseFloat(byte.toFixed(2)) + " B";
    }
    return finalResult;
  }

  scrollToTop(): void {
    // scroll to the top of the body
    return document.body.scrollIntoView({
      behavior: 'smooth',
      block: 'start',
      inline: 'start',
    });
  }
}
function isJsonString(str: string) {
  try {
    JSON.parse(str);
  } catch (e) {
    return false;
  }
  return true;
}