import { Observable, Observer } from 'rxjs';
import { AnonymousSubject } from 'rxjs/internal/Subject';
import { Subject } from 'rxjs';
import { map, share } from 'rxjs/operators';
import { environment } from 'src/environments/environment';

// const WS_URL = "wss://echo.websocket.events"; //websocket url
// const WS_URL = `ws://${location.host}`; //websockent url
const WS_URL: String = environment.websocketUrl;

export class WebsocketService {
  private subject: any;
  public messages: Subject<Object>;
  private ws?: WebSocket;
  private url?: String;

  constructor() {
    this.url = WS_URL;
    this.messages = <Subject<Object>>this.connect(WS_URL).pipe(
      map(
        (response: MessageEvent): Object => {
          return response.data;
        }
      )
    );
  }

  public connect(url: any): AnonymousSubject<MessageEvent> {
    if (!this.subject) {
      this.subject = this.create(url);
      console.log("Successfully connected: " + url);
    }
    return this.subject;
  }

  private create(url: any): AnonymousSubject<MessageEvent> {
    this.ws = new WebSocket(url);
    let observable = new Observable((obs: Observer<MessageEvent>) => {
      this.ws!.onmessage = obs.next.bind(obs);
      this.ws!.onerror = obs.error.bind(obs);
      this.ws!.onclose = obs.complete.bind(obs);
      return this.ws!.close.bind(this.ws);
    })
    // .pipe(
    // share() //multicasting the observable
    // );
    console.log(WS_URL);
    let observer: Observer<MessageEvent> = {
      error: (err: any) => {
        console.log("Error incurred: " + err);
      }, complete: () => {
        console.log("Connection closed");
      },
      next: (data: Object) => {
        console.log('Message sent to websocket: ', data);
        if (this.ws!.readyState === WebSocket.OPEN) {
          this.ws!.send(JSON.stringify(data));
        }
      }
    };
    return new AnonymousSubject<MessageEvent>(observer, observable);
  }

  public isClosed() : boolean {
    let isClosed = (this.ws?.readyState == WebSocket.CLOSED)
    if (isClosed) { console.log("Websocket is closed") }
    return isClosed
  }

  public close(): void {
    // if (!this.subject) {
    //   this.subject = this.close();
    //   console.log("Successfully closed: " + this.url!);
    // }
    // return this.subject;

    this.subject = undefined;

    //close a websocket
    this.ws?.close();
  }

  public reconnect():void {
    this.url = WS_URL;
    this.messages = <Subject<Object>>this.connect(WS_URL).pipe(
      map(
        (response: MessageEvent): Object => {
          return response.data;
        }
      )
    );
  }
}

