import { Injectable } from "@angular/core";
import { DeviceLogContent } from "../device-log-content";
import { TranslateService } from "@ngx-translate/core";

@Injectable()
export class CSVService {
    private headColumns = ['Type', 'System', 'Floor', 'Time', 'Designation', 'Body_EN'];
    private translateEN = ['Type', 'System', 'Floor', 'Time', 'Designation', 'Body'];
    private translateCN = ['類型', '系統', '樓層', '時間', '稱號', '內容'];
    private translateSC = ['类型', '系统', '楼层', '时间', '称号', '内容'];
    private currentLanguage = "en";
    //Note: To convert json to csv file, 
    //Step 0: Check app.module.ts, whether CSVService has been included
    //Step 1: Inject the CSVService in your constructor (e.g. constructor(private service: CSVService))
    //Step 2: call downloadFile (e.g this.service.downloadFile(jsonData))

    constructor(private translate: TranslateService) {}

    //returns string with delimiter
    ConvertToCSV(objArray: any, headerList: string[]) {
        let array = typeof objArray != 'object' ? JSON.parse(objArray) : objArray;
        let str = '';
        let row = this.translate.instant("sequence_no") + ',';
        for (let index in headerList) {
            // row += headerList[index] + ',';
            switch (this.currentLanguage) {
                case "zh_HK":
                    row += this.translateCN[index] + ",";
                    break;
                case "zh_CN":
                    row += this.translateSC[index] + ",";
                    break;
                default:
                    row += this.translateEN[index] + ",";
            }
        }
        row = row.slice(0, -1);
        str += row + '\r\n'; //S.No.
        for (let i = 0; i < array.length; i++) {
            let line = (i + 1) + '';
            for (let index in headerList) {
                let head = headerList[index];
                line += ',' + array[i][head];
            }
            str += line + '\r\n';
        }
        return str;
    }

    getType(data: DeviceLogContent) {
        switch(this.currentLanguage) {
            case "zh_HK":
                return data.type.nameCN == null ? data.type.key : data.type.nameCN;
            case "zh_CN":
                return data.type.nameSC == null ? data.type.key : data.type.nameSC;
            default:
                return data.type.nameEN == null ? data.type.key : data.type.nameEN;
        }
    }

    getSystem(data: DeviceLogContent) {
        switch(this.currentLanguage) {
            case "zh_HK":
                return data.system.nameCN == null ? data.system.key : data.system.nameCN;
            case "zh_CN":
                return data.system.nameSC == null ? data.system.key : data.system.nameSC;
            default:
                return data.system.nameEN == null ? data.system.key : data.system.nameEN;
        }
    }

    getBody(data: DeviceLogContent) {
        switch(this.currentLanguage) {
            case "zh_HK":
                return data.body.langTC == null ? data.body.langEN : data.body.langTC;
            case "zh_CN":
                return data.body.langSC == null ? data.body.langEN : data.body.langSC;
            default:
                return data.body.langEN;
        }
    }

    //returns string with delimiter
    ConvertToCSVFromDeviceLogContent(contentJSON: any, headerList: string[]) {
        let array = contentJSON;
        let str = '';
        let row = this.translate.instant("sequence_no") + ',';
        for (let index in headerList) {
            // row += headerList[index] + ',';
            switch (this.currentLanguage) {
                case "zh_HK":
                    row += this.translateCN[index] + ",";
                    break;
                case "zh_CN":
                    row += this.translateSC[index] + ",";
                    break;
                default:
                    row += this.translateEN[index] + ",";
            }
        }
        row = row.slice(0, -1);
        str += row + '\r\n';
        for (let i = 0; i < array.length; i++) {
            let line = (i + 1) + ''; //S.No.
            for (let index in headerList) {
                let head = headerList[index];
                if (headerList[index] == "Type") {
                    // line += "," + array[i].type.key
                    line += "," + this.getType(array[i])
                } else if (headerList[index] == "System") {
                    // line += "," + array[i].system.key
                    line += "," + this.getSystem(array[i])
                } else if (headerList[index] == "Body_EN") {
                    // line += "," + array[i].body.langEN
                    line += "," + this.getBody(array[i])
                } else {
                    let headLowercase = head.toLowerCase()
                    line += "," + array[i][headLowercase]
                }
            }
            str += line + '\r\n';
        }
        return str;
    }

    //call this
    downloadFile(data: any, filename = 'data', currentLanguage: string) {
        let csvData;
        this.currentLanguage = currentLanguage;
        // if (data[0]._id != undefined) {
            //raw JSON return => CSV
            // csvData = this.ConvertToCSV(data, this.headColumns);
        // } else {
            //DeviceLogContent => CSV
            csvData = this.ConvertToCSVFromDeviceLogContent(data, this.headColumns);
        // }
        console.log(csvData)
        let blob = new Blob(['\ufeff' + csvData], { type: 'text/csv;charset=utf-8;' });
        let dwldLink = document.createElement("a");
        let url = URL.createObjectURL(blob);
        let isSafariBrowser = navigator.userAgent.indexOf('Safari') != -1 && navigator.userAgent.indexOf('Chrome') == -1;
        if (isSafariBrowser) {  //if Safari open in new window to save file with random filename.
            dwldLink.setAttribute("target", "_blank");
        }
        dwldLink.setAttribute("href", url);
        dwldLink.setAttribute("download", filename + ".csv");
        dwldLink.style.visibility = "hidden";
        document.body.appendChild(dwldLink);
        dwldLink.click();
        document.body.removeChild(dwldLink);
    }
}