import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-project-submit-dialog',
  templateUrl: './add-group-filter-submit-dialog.component.html',
  styleUrls: ['./add-group-filter-submit-dialog.component.css']
})
export class AddGroupFilterSubmitDialogComponent {
  constructor(private router: Router, private dialogRef: MatDialogRef<AddGroupFilterSubmitDialogComponent>) {}

  addNew() {
    this.dialogRef.close({'reset': true}); //close dialog
  }

  navigateTo (path: string) {
    this.dialogRef.close({'path': path}); //close dialog
  }
}
