import { Component, EventEmitter, Input, Output } from '@angular/core';
import { DeviceLog } from 'src/app/device-log-content';

@Component({
  selector: 'app-submit-query-actions',
  templateUrl: './submit-query-actions.component.html',
  styleUrls: ['./submit-query-actions.component.css']
})
export class SubmitQueryActionsComponent {
  @Input()
  disabled: boolean;

  @Input()
  deviceLogs?: DeviceLog;

  @Output()
  reset = new EventEmitter<void>();

  @Output()
  submit = new EventEmitter<void>();

  @Output()
  export = new EventEmitter<void>();

  isObj(object: any) {
    return typeof (object) == 'object';
  }
}
