export class GroupFilter {
  id?: string;
  systemId?: string;
  nameEN?: string;
  nameTC?: string;
  nameSC?: string;
  equipmentIds?: string[];

  constructor(options?: {
    id?: string;
    systemId?: string;
    nameEN?: string;
    nameTC?: string;
    nameSC?: string;
    equipmentIds?: string[];
  }) {
    this.id = options.id ? options.id : new Date().getTime().toString();
    this.systemId = options?.systemId;
    this.nameEN = options?.nameEN;
    this.nameTC = options?.nameTC;
    this.nameSC = options?.nameSC;
    this.equipmentIds = [];
  }
}

export class GroupDetailEditingResult {
  action?: string;
  username?: string;
  result?: string;
  order?: string;
  systemId?: string;
  errorCode?: number;

  fromJSON(groupDetailEditingResult: any) {
    return new GroupDetailEditingResult({
      action: groupDetailEditingResult.action ?? '',
      username: groupDetailEditingResult.username ?? '',
      result: groupDetailEditingResult.result ?? '',
      order: groupDetailEditingResult.order ?? '',
      systemId: groupDetailEditingResult.system_ID ?? '',
      errorCode: groupDetailEditingResult.error_Code ?? 0,
    });
  }

  constructor(options?: {
    action?: string;
    username?: string;
    result?: string;
    order?: string;
    systemId?: string;
    errorCode?: number;
  }) {
    this.action = options?.action;
    this.username = options?.username;
    this.result = options?.result;
    this.order = options?.order;
    this.systemId = options?.systemId;
    this.errorCode = options?.errorCode;
  }
}
