import { Equipment } from "./equipment-setting";

//For Add or Modify System Detail Use
export class SystemDetail {
    order?: number;
    systemID?: string;
    system_EN?: string;
    system_CN?: string;
    system_SC?: string;
    equipments?: Equipment[];
  
    constructor(options?: {
      order?: number;
      systemID?: string;
      system_EN?: string;
      system_CN?: string;
      system_SC?: string;
      equipments?: Equipment[];
    }) {
      this.order = options?.order;
      this.systemID = options?.systemID ?? new Date().getTime().toString();
      this.system_EN = options?.system_EN;
      this.system_CN = options?.system_CN;
      this.system_SC = options?.system_SC;
      this.equipments = options?.equipments;
    }
  }