import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { Location } from '@angular/common';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { AddSystemComponent } from '../add-system/add-system.component';
import { SystemUtils } from '../system-utils';
import { ProjectUtils } from 'src/app/project/project';
import { SystemDetail } from 'src/app/equipment-setting';
import { Message, MessageUtils } from 'src/app/message';
import { UserService } from 'src/app/service/user.service';
import { WebsocketService } from 'src/app/service/websocket.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-system-list',
  templateUrl: './system-list.component.html',
  styleUrls: ['./system-list.component.css'],
})
export class SystemListComponent {
  static route = 'system-list';

  showMenu = true;
  siteLanguage = 'English';
  languageList = [
    { code: 'en', label: 'English' },
    { code: 'zh_HK', label: '繁體中文' },
    { code: 'zh_CN', label: '简体中文' },
  ];
  currentLanguage?: String;

  systemList = [];
  systemReordered = [];
  projectName;

  isTheSame = true;
  updateTime;
  //environment configuration (refer to /environment)
  isDev: boolean = false;

  getProjectName() {
    let project = ProjectUtils.getProjectFromLocalStorage();

    console.log('project detail : ', project);
    return project.project_EN;
  }

  sortArray(systemList: SystemDetail[]) {
    systemList.sort((a, b) => {
      if (a.order > b.order) {
        return 1;
      }
      if (a.order < b.order) {
        return -1;
      }
      return 0;
    });

    //reassign the order in the system object
    this.systemList = SystemUtils.reassignOrder(systemList);
    this.systemReordered = Object.assign([], this.systemList);
  }

  setLanguage(translate: TranslateService) {
    let savedLang = localStorage.getItem('LANG');
    if (savedLang != null) {
      this.siteLanguage = savedLang;
      translate.use(savedLang);
      this.currentLanguage = translate.currentLang;
      console.log('currentLanguage', this.currentLanguage);
    }
  }

  getSystemsFromLocalStorage() {
    //retrieve the list from local storage
    let systemArr = SystemUtils.getListFromLocalStorage();
    this.sortArray(systemArr);
  }

  constructor(
    private translate: TranslateService,
    private router: Router,
    private userService: UserService,
    private location: Location,
    private websocketService: WebsocketService
  ) {
    this.getUpdateTime();

    console.log('production? : ' + environment.production);
    this.isDev = !environment.production;

    this.setLanguage(translate);
    this.projectName = this.getProjectName();

    websocketService.reconnect();
    console.log('++++++++ reconnect websocket +++++++++');
    websocketService.messages.subscribe((msg) => {
      console.log('[system-list] Response from websocket: ' + msg);

      if (MessageUtils.isJsonString(msg.toString())) {
        var jObject = JSON.parse(msg.toString());
        if (jObject.type == 'system_ListEditing_Result') {
          //After submit
          let content = jObject.content;

          if (content.result == 'Success') {
            //Copy reordered results to original list and save the changes
            this.systemList = Object.assign([], this.systemReordered);
            this.isTheSame = true; 
            SystemUtils.saveListToLocalStorage(this.systemReordered);
            alert("Success")
          } 
        }
      }
    });

    setTimeout(() => {
      this.getSystemsFromLocalStorage();
    }, 1000);
  }

  drop(event: CdkDragDrop<string[]>) {
    console.log('Swap orders between two items');

    moveItemInArray(
      this.systemReordered,
      event.previousIndex,
      event.currentIndex
    );

    //compare two arrays are the same ?
    this.isTheSame = this.compareArray(this.systemList, this.systemReordered);

    //reassign the order in the system object
    this.systemReordered = SystemUtils.reassignOrder(this.systemReordered);
  }

  getSystemOrderAndId() {
    let newSystemArray = [];
    this.systemReordered.forEach((system) => {
      newSystemArray.push({
        system_ID: system.systemID,
        system_Name: system.system_EN,
        order: system.order,
      });
    });

    return newSystemArray;
  }

  sendUpdateSystemListOrdering() {
    let updateMessage: Message = {
      type: '',
      content: {},
    };

    updateMessage.type = 'system_ListEditing';
    updateMessage.content = {
      username: this.userService.getUsername(),
      project_ID: localStorage.getItem('projectId'),
      update_time: this.updateTime,
      systems: this.getSystemOrderAndId(),
    };

    this.websocketService.messages.next(updateMessage);

    if (this.isDev) {
      this.sendUpdateSystemListOrderingResponse();
    }
  }

  //for testing purpose
  sendUpdateSystemListOrderingResponse() {
    let updateMessage: Message = {
      type: '',
      content: {},
    };
    updateMessage.type = 'system_ListEditing_Result';
    updateMessage.content = {
      username: this.userService.getUsername(),
      result: 'Success',
    };

    this.websocketService.messages.next(updateMessage);
  }

  getUpdateTime() {
    //Get Time API
    const request: RequestInfo = new Request(
      'https://worldtimeapi.org/api/timezone/Asia/Hong_Kong',
      {
        method: 'GET',
      }
    );

    return (
      fetch(request)
        // the JSON body is taken from the response
        .then((res) => {
          let promise = res.json();
          promise.then((value) => {
            console.log('update time : ', value.unixtime);
            this.updateTime = value.unixtime;
          });
        })
        .catch((err) => {
          console.log('get time erorr: ', err);
          this.updateTime = (Date.now() / 1000).toFixed(0);
        })
    );
  }

  saveChanges() {
    this.sendUpdateSystemListOrdering();
  }

  discardChanges() {
    this.systemReordered = Object.assign([], this.systemList);
    this.compareArray(this.systemList, this.systemReordered);
  }

  compareArray(oldArray, newArray) {
    return JSON.stringify(oldArray) == JSON.stringify(newArray);
  }

  changeSiteLanguage(localeCode: string): void {
    const selectedLanguage = this.languageList
      .find((language) => language.code === localeCode)
      ?.label.toString();

    if (selectedLanguage) {
      this.siteLanguage = selectedLanguage;
      this.translate.use(localeCode);
      console.log('localCode', localeCode);
      localStorage.setItem('LANG', localeCode);
    }
    setTimeout(() => {
      this.currentLanguage = this.translate.currentLang;
      console.log('currentLanguage', this.currentLanguage);
    }, 300);
  }

  createNew() {
    this.navigateTo(AddSystemComponent.route);
  }

  editSystem(system: SystemDetail) {
    //TODO: find a better way to pass system data
    this.navigateTo(AddSystemComponent.route, {
      edit: true,
      system: JSON.stringify(system),
    });
  }

  navigateTo(path: string, customParams?: any) {
    if (customParams != null) {
      this.router.navigate([path], { queryParams: customParams });
    } else {
      this.router.navigateByUrl(path);
    }
  }

  backToLastPage() {
    this.location.back();
  }
}
