import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControlName, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-filter-log-date-picker',
  templateUrl: './filter-log-date-picker.component.html',
  styleUrls: ['./filter-log-date-picker.component.css']
})
export class FilterLogDatePickerComponent implements OnInit {
  ngOnInit(): void {
    console.log("date : ", this.date);
  }
  @Input()
  fg: FormGroup;

  @Input()
  date: Date;

  @Input()
  controlName: FormControlName;
  
  @Output()
  onDateChanged = new EventEmitter<Date>();

  onChange(result: Date) {
    console.log("onEndDateChange(1)", result);
    this.date = result;
    this.onDateChanged.emit(this.date);
  }

  /*onDateChange(result: Date): void {
    console.log("onEndDateChange(2)", result)  // => '2015-01-26T06:40:36.181'
  }*/
}
